import React, { Component } from "react";

class InformationServer extends Component {
  render() {
    return (
      <div>
        {this.props.info.php_uname} <br />
        WEB SERVER : <font color="lime">{this.props.info.webserver}</font> <br />
        SERVER IP <font color="lime">{this.props.info.ip.server}</font> / YOUR
        IP <font color="lime">{this.props.info.ip.me}</font>
        <br />
        USER / GROUP:{" "}
        <font color="lime">
          {this.props.info.usergroup.user} / {this.props.info.usergroup.group}
        </font>{" "}
        <br />
        CPU / RAM : <font color="lime">
          {this.props.info.server.cpu.load}%
        </font>{" "}
        (Cores: <font color="lime">{this.props.info.server.cpu.cores}</font>) /{" "}
        <font color="lime">{this.props.info.server.ram.usage}</font> -{" "}
        <font color="lime">{this.props.info.server.ram.total}</font> (
        <font color="lime">{this.props.info.server.ram.percentage}%</font>) <br />
        HDD : <font color="lime">{this.props.info.hdd.used}</font> /{" "}
        <font color="lime">{this.props.info.hdd.size}</font> (Free:{" "}
        <font color="lime">{this.props.info.hdd.free}</font>) <br />
      </div>
    );
  }
}

export default InformationServer;
