import React, { Component } from "react";

// Library
import axios from "axios";
import $ from "jquery";
import ReactBSAlert from "react-bootstrap-sweetalert";

// style
import Loading from "../../components/Loading";

// Components
import Header from "../../components/Header";
import Table from "../../components/Table";

// https://sonar.omnisint.io/reverse/103.251.44.198

const api = "https://sonar.omnisint.io/reverse/";

export default class PanelRevip extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //wajib
      error: false,
      isLoaded: false,
      alertModal: null,

      ip: null,
      list: [],
      add: 0,
      notice: null,
    };
  }

  // handler
  handleInputChange = (event) => {
    const target = event.target;
    const name = target.name;
    var value = target.type === "checkbox" ? target.checked : target.value;

    if (value.length === 0) {
      value = null;
    }

    this.setState({
      [name]: this.hanyaAngka(value),
    });
  };

  AnimasiLoading = () => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </ReactBSAlert>
      ),
    });
  };

  ShowModal = (onShow, content, width = 500, onTop = false) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            color: "black",
            width: width,
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={true}
          closeOnClickOutside={false}
          customClass={{
            closeButton: "close-button-class",
          }}
          //
          onConfirm={() => this.closeModal(onTop)}
          onCancel={() => this.closeModal(onTop)}
          //
          afterMount={() => onShow()}
        >
          {content}
        </ReactBSAlert>
      ),
    });
  };
  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };

  matchArray = (string, array) => {
    var found = false;
    for (var i = 0; i < array.length && !found; i++) {
      if (
        String(array[i]).toLocaleLowerCase() ===
        String(string).toLocaleLowerCase()
      ) {
        found = true;
        break;
      }
    }
    return found;
  };

  execute = () => {
    const { ip } = this.state;
    this.AnimasiLoading();

    const setState = (array) => {
      this.setState(array);
    };
    const closeModal = () => {
      this.closeModal();
    };
    const matchArray = (string, array) => {
      return this.matchArray(string, array);
    };

    setState({
      ip: null, // reset
    });
    document.getElementById("ip").value = null;
    document.getElementById("submit").style.display = "none";

    var add = 0;
    axios
      .get(api + ip)
      .then(function (response) {
        const list = JSON.parse(response.request.responseText);

        // insert ke storage
        var site_bank = JSON.parse(localStorage.getItem("site_bank"));

        // console.log(list.length, site_bank.length); // debug

        for (let i = 0; i < list.length; i++) {
          const site = list[i];

          // jika site belom ada di site bank
          // if (!site_bank.indexOf(site) > -1) {
          if (matchArray(site, site_bank) === false) {
            site_bank.push(site);
            add++;
          }
        }

        // save ulang ke site_bank
        localStorage.removeItem("site_bank");
        localStorage.setItem("site_bank", JSON.stringify(site_bank));

        // show to table
        setState({
          list,
          add,
        });
      })
      .catch(function (error) {
        setState({
          notice: "IP tidak mempunyai site...",
        });
      })
      .finally(function () {
        closeModal();
      });
  };

  componentDidMount() {
    localStorage.setItem("site_bank", JSON.stringify([]));
    this.setState({
      isLoaded: true,
    });
    $(document).ready(function () {
      /* code here */
      document.getElementById("ip").focus();
    });
  }
  componentWillUnmount() {
    localStorage.removeItem("site_bank");
  }

  getDate() {
    var today = new Date();
    return (
      today.getFullYear() +
      "_" +
      (today.getMonth() + 1) +
      "_" +
      today.getDate() +
      "-" +
      today.getHours() +
      "_" +
      today.getMinutes() +
      "_" +
      today.getSeconds()
    );
  }

  downloadList = (list) => {
    const element = document.createElement("a");
    const file = new Blob([list.join("\n")], {
      type: "text/plain;charset=utf-8",
    });

    element.href = URL.createObjectURL(file);
    element.download = "revip-" + this.getDate() + ".txt";
    document.body.appendChild(element);

    element.click();

    element.remove();
  };

  hanyaAngka(key, space = false) {
    var hanya_angka = key;
    const tidak_boleh = [
      "q",
      "w",
      "e",
      "r",
      "t",
      "y",
      "u",
      "i",
      "o",
      "p",
      "a",
      "s",
      "d",
      "f",
      "g",
      "h",
      "j",
      "k",
      "l",
      "z",
      "x",
      "c",
      "v",
      "b",
      "n",
      "m",

      "`",
      "~",
      "!",
      "@",
      "#",
      "$",
      "%",
      "^",
      "&",
      "*",
      "(",
      ")",
      "_",
      "-",
      "+",
      "=",
      "[",
      "]",
      "{",
      "}",
      "\\",
      "|",
      ":",
      ";",
      "'",
      '"',
      "<",
      ",",
      ">",
      // ".", // ip pake titik boss :)
      "?",
      "/",
    ];
    if (space) {
      tidak_boleh.push(" ");
    }
    if (hanya_angka !== null) {
      for (let i = 0; i < tidak_boleh.length; i++) {
        hanya_angka = hanya_angka.replaceAll(tidak_boleh[i], "");
      }
    }
    return hanya_angka;
  }

  render() {
    document.title = this.props.title;
    const { error, isLoaded, alertModal, ip, list, add, notice } = this.state;

    if (error) {
      return <>{error}</>;
    } else if (!isLoaded) {
      return <> Loading </>;
    } else {
      return (
        <>
          <Header app_name={this.props.title.split(" | ")[0]} />

          <div style={{ textAlign: "center" }}>
            <input
              type="text"
              name="ip"
              id="ip"
              value={ip}
              onChange={(e) => {
                this.handleInputChange(e);
                if (e.target.value.length > 0) {
                  document.getElementById("submit").style.display = "";
                } else {
                  document.getElementById("submit").style.display = "none";
                }
              }}
              placeholder="Masukan IP Address"
              onKeyPress={(event) => {
                if (event.which === 13) {
                  // enter
                  if (event.target.value.length > 0) {
                    event.preventDefault();
                    this.execute();
                  }
                }
              }}
            />
            <br />
            {notice ? (
              <>
                <span style={{ color: "red", fontSize: 14 }}>{notice}</span>
                <br />
              </>
            ) : add > 0 ? (
              <>
                <span style={{ color: "lime", fontSize: 14 }}>
                  {add} site ditambahkan !!
                </span>
                <br />
              </>
            ) : list.length > 0 ? (
              <>
                <span style={{ color: "red", fontSize: 14 }}>
                  tidak ada site yang ditambahkan !!
                </span>
                <br />
              </>
            ) : null}
            <br />

            <input
              type="submit"
              id="submit"
              style={{
                display: "none",
                background: "transparent",
                color: "lime",
              }}
              value="Revip!"
              onClick={(e) => {
                e.preventDefault();
                this.setState({
                  notice: null,
                });
                this.execute();
              }}
            />
            <br />
            <br />

            {list.length > 0 ? (
              <>
                <input
                  type="button"
                  style={{
                    background: "transparent",
                    color: "lime",
                  }}
                  value={`Site Bank (${
                    JSON.parse(localStorage.getItem("site_bank")).length
                  })`}
                  onClick={(e) => {
                    e.preventDefault();
                    // this.ShowModal(function () {
                    //   console.log(
                    //     JSON.parse(localStorage.getItem("site_bank"))
                    //   );
                    //   // eslint-disable-next-line
                    // }, <>Buka Inspect Browser -{">"} Console</>);
                  }}
                />
                &nbsp;&nbsp;&nbsp;
                <input
                  type="button"
                  style={{
                    background: "transparent",
                    color: "lime",
                  }}
                  value="Download List (txt) !!"
                  onClick={(e) => {
                    e.preventDefault();
                    this.downloadList(
                      JSON.parse(localStorage.getItem("site_bank"))
                    );
                  }}
                />
                <br />
                <br />
                <Table
                  {...this.props}
                  column={["Site", "Action"]}
                  row={
                    <>
                      {list.map((val) => {
                        return (
                          <tr>
                            <td class="td_home">{val}</td>
                            <td class="td_home">
                              <a
                                href={"http://" + val}
                                target="_blank"
                                rel="noreferrer"
                              >
                                New Tab
                              </a>
                            </td>
                          </tr>
                        );
                      })}
                    </>
                  }
                />
              </>
            ) : null}
          </div>

          {alertModal}
        </>
      );
    }
  }
}
