import React, { Component } from "react";

class header extends Component {
  handleClick = (e) => {
    e.preventDefault();
  };
  render() {
    return (
      <>
        <div class="row">
          <div class="headerlogo four columns">
            <div class="logo">
              <a href="!#" onClick={this.handleClick}>
                <h4>P34C3_KHYREIN</h4>
              </a>
            </div>
          </div>
          <div class="headermenu eight columns noleftmarg">
            <nav id="nav-wrap">
              <ul id="main-menu" class="nav-bar sf-menu">
                <li>
                  <a href="!#" onClick={this.handleClick}>
                    Tools
                  </a>
                  <ul>{this.props.pages}</ul>
                </li>
                <li>
                  <a href="!#" onClick={this.handleClick}>
                    Github Repository
                  </a>
                  <ul>
                    {this.props.repos
                      .filter((val) => {
                        return !this.props.except.some((v) =>
                          val.name.includes(v)
                        );
                      })
                      .map((repo) => {
                        return (
                          <>
                            <li>
                              <a
                                href={repo.html_url}
                                target="_blank"
                                rel="noreferrer"
                              >
                                {repo.name}
                              </a>
                            </li>
                          </>
                        );
                      })}
                  </ul>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </>
    );
  }
}

export default header;
