import React, { Component } from "react";

class BoxMenu extends Component {
  handleClick = (e) => {
    e.preventDefault();
  };
  render() {
    return (
      <>
        <div class="row">
          <div
            class="twelve columns"
            style={{ textAlign: "center", marginLeft: 40 }}
          >
            <ul
              class="ca-menu"
              style={{
                listStyleType: "none",
                display: "block",
                justifyContent: "center",
                margin: 0,
                padding: 0,
              }}
            >
              {this.props.pages}
            </ul>
          </div>
        </div>
      </>
    );
  }
}

export default BoxMenu;
