import React, { Component } from "react";

// style
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import Header from "./header/index";

class Container extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //wajib
    };
  }

  render() {
    return (
      <div>
        <Header
          info={this.props.info}
          path={this.props.path}
          isExplore={this.props.isExplore}
          pages={this.props.pages}
          {...this.props}
          list={(v) => {
            this.props.list(v);
          }}
          change_path={(v) => {
            this.props.change_path(v);
          }}
        />
        {this.props.children}
        <br />
        <center>
          <h4 style={{ border: "" }} className="footer">
            P34C3_KHYREIN Shell V1.2
          </h4>
        </center>
        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
      </div>
    );
  }
}

export default Container;
