import React, { Component } from "react";

class Table extends Component {
  render() {
    return (
      <table
        width="100%"
        className="table_home"
        border="0"
        cellpadding="3"
        cellspacing="1"
        align="center"
      >
        <tr>
          {this.props.column.map((val) => {
            return (
              <th className="th_home">
                <center>{val}</center>
              </th>
            );
          })}
        </tr>
        {this.props.first}
        {this.props.row}
      </table>
    );
  }
}

export default Table;
