import React, { Component } from "react";

// Library
import axios from "axios";
import ReactBSAlert from "react-bootstrap-sweetalert";

// Components
import Header from "../../components/Header";

// style
import Loading from "../../components/Loading";
import Table from "../../components/Table";

// Function
function randomString(length = 10) {
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

class FakeForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //wajib
      error: false,
      isLoaded: false,
      alertModal: null,

      url: null,

      // add
      element: 0,
      ifFile: 0,
      headers: [],
      method: null,

      print: null,
    };
  }

  AnimasiLoading = () => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </ReactBSAlert>
      ),
    });
  };

  ShowModal = (onShow, content, width = 500, onTop = false) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            color: "black",
            width: width,
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={true}
          closeOnClickOutside={false}
          customClass={{
            closeButton: "close-button-class",
          }}
          //
          onConfirm={() => this.closeModal(onTop)}
          onCancel={() => this.closeModal(onTop)}
          //
          afterMount={() => onShow()}
        >
          {content}
        </ReactBSAlert>
      ),
    });
  };
  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };

  PrintResponse = (response) => {
    console.log(response.replaceAll("<br />", ""));
    this.setState({
      print: response,
    });

    setTimeout(() => {
      window.scrollTo(0, document.body.scrollHeight);
    }, 1000);
  };

  render() {
    document.title = this.props.title;
    const { alertModal, element, url, ifFile, headers, method, print } =
      this.state;

    // // submit
    // headers
    //   .map((val) => {
    //     return Object.keys(val);
    //   })
    //   .some((v) => "Content-Type".includes(v));

    return (
      <div
        style={{
          color: "white",
          textAlign: "center",
        }}
      >
        <Header app_name={this.props.title.split(" | ")[0]} />
        <input
          type="text"
          value={url}
          onChange={(e) => {
            this.setState({
              url: e.target.value,
            });
          }}
          onBlur={(e) => {
            const value = e.target.value;
            if (value.length >= 10) {
              if (!value.includes("http://")) {
                if (!value.includes("https://")) {
                  this.setState({
                    url: "http://" + e.target.value,
                  });
                }
              }
            }
          }}
          placeholder="URL Site target?"
          style={{
            marginBottom: 30,
            color: "black",
          }}
        />
        <br />
        {String(url).length < 10 ? (
          <span>
            link test request?{" "}
            <a
              href="#insert-test"
              onClick={(e) => {
                e.preventDefault();
                this.setState({
                  url: "https://p34c3-khyrein.linuxploit.com/test_request.php",
                });
              }}
            >
              klik !!
            </a>
          </span>
        ) : null}

        <br />

        {String(url).length >= 10 ? (
          method !== null ? (
            <>
              <h4>Method : {method.toUpperCase()}</h4>
              <br />
              <br />
              <input
                type="button"
                value="Add Elements"
                style={{
                  backgroundColor: "transparent",
                  color: "lime",
                  marginBottom: 30,
                }}
                onClick={() => {
                  this.ShowModal(
                    function () {
                      //
                    },
                    <>
                      <h3>Add Elements</h3>
                      <select
                        id="type"
                        onChange={(e) => {
                          const name = document.getElementById("name"),
                            placeholder = document.getElementById("ph"),
                            button_add = document.getElementById("button_add");

                          name.style.display = "";
                          placeholder.style.display = "none";
                          button_add.style.display = "none";
                        }}
                        style={{ width: 280 }}
                      >
                        <option disabled hidden selected>
                          pilih type input
                        </option>
                        <option value="text">text</option>
                        {method === "post" ? (
                          <option value="file">file</option>
                        ) : null}
                      </select>
                      <input
                        id="name"
                        type="text"
                        style={{ display: "none" }}
                        placeholder="name?"
                        onKeyUp={(e) => {
                          // gathering
                          const type = document.getElementById("type"),
                            placeholder = document.getElementById("ph"),
                            button_add = document.getElementById("button_add");

                          if (e.target.value.length > 0) {
                            if (type.value === "text") {
                              placeholder.style.display = "";
                            } else {
                              button_add.style.display = "";
                            }
                          } else {
                            placeholder.style.display = "none";
                            button_add.style.display = "none";
                          }
                        }}
                      />
                      <input
                        id="ph"
                        type="text"
                        style={{ display: "none" }}
                        placeholder="placeholder?"
                        onKeyUp={(e) => {
                          // gathering
                          const button_add =
                            document.getElementById("button_add");

                          if (e.target.value.length > 0) {
                            button_add.style.display = "";
                          } else {
                            button_add.style.display = "none";
                          }
                        }}
                      />
                      <br />
                      <br />
                      <button
                        id="button_add"
                        type="button"
                        style={{ display: "none" }}
                        onClick={() => {
                          // gathering
                          const type = document.getElementById("type"),
                            name = document.getElementById("name"),
                            placeholder = document.getElementById("ph");

                          // get random
                          const random = randomString();

                          // insert
                          var divs = document.createElement("div"); // start
                          divs.id = "div_" + random;

                          //input
                          var inputs = document.createElement("input");
                          inputs.size = 8;
                          inputs.type = type.value;
                          inputs.name = name.value;
                          if (placeholder.value !== undefined) {
                            inputs.placeholder = placeholder.value;
                          }
                          //button X
                          var spans = document.createElement("span");
                          spans.innerHTML = "&nbsp;X";
                          spans.style.color = "red";
                          spans.style.cursor = "pointer";
                          spans.onclick = (e) => {
                            document.getElementById("div_" + random).remove();
                            this.setState({
                              element: this.state.element - 1,
                              ifFile:
                                type.value === "file"
                                  ? this.state.ifFile - 1
                                  : this.state.ifFile,
                            });
                          };

                          // include
                          divs.appendChild(inputs);
                          divs.appendChild(spans); // end

                          document.getElementById("add").appendChild(divs);

                          this.setState({
                            element: this.state.element + 1,
                            ifFile:
                              type.value === "file"
                                ? this.state.ifFile + 1
                                : this.state.ifFile,
                          });

                          this.closeModal();
                        }}
                      >
                        Add !!
                      </button>
                    </>,
                    300
                  );
                }}
              />
              &nbsp;&nbsp;&nbsp;&nbsp;
              <input
                type="button"
                value="Add Headers"
                style={{
                  backgroundColor: "transparent",
                  color: "lime",
                  marginBottom: 30,
                }}
                onClick={() => {
                  this.ShowModal(
                    function () {
                      //
                    },
                    <>
                      <h3>Add Headers</h3>

                      <input
                        id="key"
                        type="text"
                        placeholder="key?"
                        onKeyUp={(e) => {
                          // gathering
                          const value = document.getElementById("value"),
                            button_add = document.getElementById("button_add");

                          if (e.target.value.length > 0) {
                            value.style.display = "";
                            if (value.value.length > 0) {
                              button_add.style.display = "";
                            } else {
                              button_add.style.display = "none";
                            }
                          } else {
                            value.style.display = "none";
                            button_add.style.display = "none";
                          }
                        }}
                      />
                      <br />
                      <br />
                      <input
                        id="value"
                        type="text"
                        style={{ display: "none", width: "100%" }}
                        placeholder="value?"
                        onKeyUp={(e) => {
                          // gathering
                          const button_add =
                            document.getElementById("button_add");

                          if (e.target.value.length > 0) {
                            button_add.style.display = "";
                          } else {
                            button_add.style.display = "none";
                          }
                        }}
                      />
                      <br />
                      <br />
                      <button
                        id="button_add"
                        type="button"
                        style={{ display: "none" }}
                        onClick={() => {
                          // gethering
                          const key = document.getElementById("key").value,
                            value = document.getElementById("value").value;

                          var header = this.state.headers;

                          // console.log("awal:", header);
                          header = header.concat({
                            [key]: value,
                          });
                          // console.log("akhir:", header);

                          this.setState({
                            headers: header,
                          });
                          this.closeModal();
                        }}
                      >
                        Add !!
                      </button>
                    </>
                  );
                }}
              />
              <br />
              {ifFile > 0 || headers.length > 0 ? (
                <div>
                  <h4>Header</h4>
                  <Table
                    column={["key", "value", "action"]}
                    first={
                      <>
                        {ifFile > 0 ? (
                          <>
                            <tr>
                              <td
                                class="td_home"
                                style={{ textAlign: "center" }}
                              >
                                Content-Type
                              </td>
                              <td
                                class="td_home"
                                style={{ textAlign: "center" }}
                              >
                                multipart/form-data
                              </td>
                              <td
                                class="td_home"
                                style={{ textAlign: "center" }}
                              >
                                required (file include)
                              </td>
                            </tr>
                          </>
                        ) : null}
                      </>
                    }
                    row={
                      <>
                        {headers
                          .filter((val) => {
                            return ifFile > 0
                              ? val["Content-Type"] === undefined
                                ? true
                                : false
                              : true;
                          })
                          .map((val) => {
                            return (
                              <tr>
                                <td
                                  class="td_home"
                                  style={{ textAlign: "center" }}
                                >
                                  {Object.keys(val)[0]}
                                </td>
                                <td
                                  class="td_home"
                                  style={{ textAlign: "center" }}
                                >
                                  {Object.values(val)[0]}
                                </td>
                                <td
                                  class="td_home"
                                  style={{ textAlign: "center" }}
                                >
                                  <span
                                    style={{ color: "red", cursor: "pointer" }}
                                    onClick={() => {
                                      this.setState({
                                        headers: headers.filter((v) => {
                                          return Object.keys(v)[0] ===
                                            Object.keys(val)[0]
                                            ? false
                                            : true;
                                        }),
                                      });
                                    }}
                                  >
                                    X
                                  </span>
                                </td>
                              </tr>
                            );
                          })}
                      </>
                    }
                  />
                </div>
              ) : null}
              <br />
              <p
                style={{
                  border: "1px solid lime",
                  padding: "16px",
                  borderRadius: "16px",
                  width: 400,
                }}
              >
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    // get url
                    const url = this.state.url;

                    // rakit headers
                    var head = {};
                    headers
                      .filter((val) => {
                        return ifFile > 0
                          ? val["Content-Type"] === undefined
                            ? true
                            : false
                          : true;
                      })
                      .map((val) => {
                        head[Object.keys(val)[0]] = Object.values(val)[0];
                        return true;
                      });
                    if (ifFile > 0) {
                      head["Content-Type"] = "multipart/form-data";
                    }
                    // console.log(head); //debug

                    // rakit form data
                    const array = Array.from(e.target.elements).filter(
                      (val) => {
                        return val.type === "submit" ? false : true;
                      }
                    );
                    var data;
                    if (method === "get") {
                      data = "?";
                      for (let i = 0; i < array.length; i++) {
                        const element = array[i];
                        if (i !== 0) {
                          data += "&";
                        }
                        data += element.name + "=" + element.value;
                        // console.log(element); //debug
                      }
                    } else {
                      data = new FormData();
                      for (let i = 0; i < array.length; i++) {
                        const element = array[i];
                        data.append(
                          element.name,
                          element.type === "file"
                            ? element.files[0]
                            : element.value
                        );
                        // console.log(element); //debug
                      }
                    }

                    // console.log(e.target.elements); //debug
                    const PrintResponse = (response) => {
                      this.PrintResponse(response);
                    };
                    const closeModal = () => {
                      this.closeModal();
                    };

                    this.AnimasiLoading();

                    if (method === "get") {
                      axios
                        .get(url + data, {
                          headers: head,
                        })
                        .then(function (response) {
                          PrintResponse(response.request.responseText);
                        })
                        .catch(function (error) {
                          console.log(error.message);
                        })
                        .finally(function () {
                          closeModal();
                        });
                    } else {
                      axios
                        .post(url, data, {
                          headers: head,
                        })
                        .then(function (response) {
                          PrintResponse(response.request.responseText);
                        })
                        .catch(function (error) {
                          console.log(error.message);
                        })
                        .finally(function () {
                          closeModal();
                        });
                    }
                  }}
                >
                  <h4>Form</h4>
                  <div id="add"></div>
                  {element > 0 ? (
                    <>
                      <input
                        type="submit"
                        value="Submit !!"
                        style={{ backgroundColor: "transparent" }}
                      />
                    </>
                  ) : (
                    <>Silahkan Tambah Element !!</>
                  )}
                </form>
              </p>
            </>
          ) : (
            <>
              <select
                onChange={(e) => {
                  this.setState({
                    method: e.target.value,
                  });
                }}
                style={{ width: 200 }}
              >
                <option disabled hidden selected>
                  pilih method
                </option>
                <option value="get">get</option>
                <option value="post">post</option>
              </select>
            </>
          )
        ) : (
          <>Silahkan masukan URL target...</>
        )}
        <br />
        <br />
        {print ? (
          <p
            style={{
              border: "1px solid lime",
              padding: "16px",
              borderRadius: "16px",
              width: "100%",
              textAlign: "left",
            }}
            dangerouslySetInnerHTML={{ __html: print }}
          />
        ) : null}

        {alertModal}
      </div>
    );
  }
}

export default FakeForm;
