import React, { Component } from "react";

// Library
import ReactBSAlert from "react-bootstrap-sweetalert";

import { insertOptionQuery } from "../../../components/anon_hackbar/Function";

import {
  kumpulan_dios_dump,
  kumpulan_dios,
  kumpulan_dump,
} from "../../../pages/anon_hackbar/static_data";

export default class BantuanQuery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //wajib
      alertModal: null,
    };
  }

  handleClick = (event) => {
    event.preventDefault();
  };

  ShowModal = (onShow, content, width = 500, onTop = false) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            color: "black",
            width: width,
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={true}
          closeOnClickOutside={false}
          customClass={{
            closeButton: "close-button-class",
          }}
          //
          onConfirm={() => this.closeModal(onTop)}
          onCancel={() => this.closeModal(onTop)}
          //
          afterMount={() => onShow()}
        >
          {content}
        </ReactBSAlert>
      ),
    });
  };
  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };

  render() {
    const { alertModal } = this.state;
    return (
      <>
        <span id="info-bantuan-query" style={{ display: "none" }}>
          butuh bantuan? (Query){" "}
          <button
            id="bantuan-query"
            onClick={(e) => {
              this.ShowModal(
                function () {
                  document
                    .getElementById("sqli-select-query-menu")
                    .addEventListener("change", function () {
                      document.getElementById(
                        "sqli-select-query"
                      ).style.display = "block";
                      if (this.value === "dios_dump") {
                        insertOptionQuery(
                          "sqli-select-query",
                          kumpulan_dios_dump,
                          "dios_dump",
                          "Pilih Dios & Dump ..."
                        );
                      } else if (this.value === "dios") {
                        insertOptionQuery(
                          "sqli-select-query",
                          kumpulan_dios,
                          "dios",
                          "Pilih Dios ..."
                        );
                      } else if (this.value === "dump") {
                        insertOptionQuery(
                          "sqli-select-query",
                          kumpulan_dump,
                          "dump",
                          "Pilih Dump ..."
                        );
                      }
                    });
                  document
                    .getElementById("sqli-select-query")
                    .addEventListener("change", function () {
                      if (this.value === "") {
                        document.getElementById(
                          "span-sqli-select-query"
                        ).style.display = "block";
                        document.getElementById(
                          "sqli-button-tempel"
                        ).style.display = "none";
                      } else {
                        document.getElementById(
                          "span-sqli-select-query"
                        ).style.display = "none";
                        document.getElementById(
                          "sqli-button-tempel"
                        ).style.display = "block";
                      }
                    });
                },
                <>
                  <strong>
                    Bantuan <u>Query</u>
                  </strong>

                  <div>
                    <select id="sqli-select-query-menu" class="select-css">
                      <option disabled selected hidden>
                        Pilih menu ...
                      </option>
                      <option value="dios">Dios</option>
                      <option value="dump">Dump</option>
                      <option value="dios_dump">Dios + Dump</option>
                    </select>

                    <select
                      id="sqli-select-query"
                      class="select-css"
                      style={{ display: "none" }}
                    ></select>
                    <span
                      style={{ display: "none" }}
                      id="span-sqli-select-query"
                    ></span>

                    <br />
                    <a
                      class="button_bantuan"
                      id="sqli-button-tempel"
                      href="#tempel"
                      onClick={(e) => {
                        this.handleClick(e);

                        var error = 0;
                        var query =
                          document.getElementById("sqli-select-query");
                        var query_value = query.value;
                        // eslint-disable-next-line
                        var query_type =
                          query.options[query.selectedIndex].getAttribute(
                            "data-type"
                          );
                        // console.log(query_value, query_type, kolom, stabilizer);

                        if (query_value === "") {
                          document.getElementById(
                            "span-sqli-select-query"
                          ).innerHTML =
                            '<font color="red">tolonglah pilih query nya bang jago...</font>';
                          document.getElementById(
                            "span-sqli-select-query"
                          ).style.display = "block";
                          error += 1;
                        }

                        if (error === 0) {
                          document.getElementById("set-query").value =
                            query_value;
                          this.closeModal();
                        }
                      }}
                    >
                      TEMPEL!
                    </a>
                  </div>
                </>
              );
            }}
          >
            klik disini
          </button>
        </span>
        {alertModal}
      </>
    );
  }
}
