import React, { Component } from "react";

class PanelTop extends Component {
  handleClick = (e) => {
    e.preventDefault();
  };
  render() {
    return (
      <>
        <div id="panel">
          <div class="row">
            <div class="twelve columns">
              <img
                src="http://www.wowthemes.net/demo/studiofrancesca/images/info.png"
                class="pics"
                alt="info"
                style={{ width: 32 }}
              />
              <div class="infotext">
                Thank you for visiting my website! I hope you Enjoy! :)
              </div>
            </div>
          </div>
        </div>
        <p class="slide" style={{ width: "100%" }}>
          <a href="#slide-turun" class="btn-slide">
            {" "}
          </a>
        </p>
      </>
    );
  }
}

export default PanelTop;
