import React, { Component } from "react";
import { Redirect } from "react-router-dom";

// library
import axios from "axios";

// style
import Loading from "../../components/Loading";

// Components
import Container from "../../components/remote_shell/Container";

class cPanelCrack extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wajib
      isLoaded: false,
      error: false,

      // login value
      session: localStorage.getItem("session")
        ? localStorage.getItem("session")
        : null,
      notice: null,

      // gathering
      info: {},
    };
  }

  sender = (perihal, url, data_object, Response, Error, Finally) => {
    const data = new FormData();
    for (let key of Object.keys(data_object)) {
      data.append(key, data_object[key]);
    }

    axios
      .post(url, data)
      .then(function (response) {
        Response(JSON.parse(response.request.responseText));
      })
      .catch(function (error) {
        Error(`Error ${perihal} : ${error.message}`);
      })
      .finally(function () {
        Finally();
      });
  };

  componentDidMount() {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;

    const { session } = this.state;
    if (session) {
      const setState = (array) => {
        this.setState(array);
      };

      this.sender(
        "componentDidMount",
        localStorage.getItem("remember_link"),
        { password: localStorage.getItem("password") },
        function (response) {
          console.log(response);
          if (response.success) {
            setState({
              session: response.session,
              info: response.info,
            });
          } else {
            setState({
              notice: response.message,
            });
          }
        },
        function (error) {
          console.log(error);
        },
        function () {
          // kosong
          setState({
            isLoaded: true,
          });
        }
      );
    } else {
      this.setState({
        isLoaded: true,
      });
    }
  }
  render() {
    const { isLoaded, error, session, info } = this.state;
    if (error) {
      return <>{error}</>;
    } else if (!isLoaded) {
      return (
        <div style={{ color: "white" }} className="center-page">
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </div>
      );
    } else {
      document.title = this.props.title;

      // anti klik kanan
      document.addEventListener("contextmenu", (event) =>
        event.preventDefault()
      );

      if (session) {
        return (
          <Container {...this.props} info={info}>
            oke
          </Container>
        );
      } else {
        return <Redirect to="/" />;
      }
    }
  }
}

export default cPanelCrack;
