import React, { Component } from "react";

// Library
import axios from "axios";

// Components
import Header from "../../components/Header";

class FormCSRF extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wajib
      error: false,
      isLoaded: false,
      alertModal: null,

      url: "",
      name: null,
      file: null,

      ready: false,
      response_src: null,
    };
  }
  componentDidMount() {
    //
  }
  componentWillUnmount() {
    //
  }

  submitOK = () => {
    const { url, name, file } = this.state;
    var ok = 0;

    if (url.length > 0) {
      // console.log("URL OK!!");
      document.getElementById("name").style.display = "";
      ok++;
    } else {
      document.getElementById("name").style.display = "none";
    }

    if (name !== null && name.length > 0) {
      // console.log("NAME OK!!");
      document.getElementById("file").style.display = "";
      ok++;
    } else {
      document.getElementById("file").style.display = "none";
    }

    if (file !== null) {
      // console.log("FILE OK!!");
      document.getElementById("submit").style.display = "";
      ok++;
    } else {
      document.getElementById("submit").style.display = "none";
    }

    if (ok >= 3) {
      console.log("siap kirim !!");
      this.setState({
        ready: true,
      });
    } else {
      document.getElementById("submit").style.display = "none";
    }
  };

  display = (showAll = false) => {
    document.getElementById("url").style.display = showAll ? "" : "none";
    document.getElementById("name").style.display = showAll ? "" : "none";
    document.getElementById("file").style.display = showAll ? "" : "none";
    document.getElementById("submit").style.display = showAll ? "" : "none";
  };

  responses = (src) => {
    console.log(src);
    document.getElementById("container_response").style.display = "";
    this.setState({
      response_src: src,
    });
  };

  render() {
    document.title = this.props.title;
    const { alertModal, url, name, file, ready, response_src } = this.state;
    return (
      <div style={{ color: "white", textAlign: "center" }}>
        <Header app_name={this.props.title.split(" | ")[0]} />
        <form
          onSubmit={(e) => {
            e.preventDefault();
            if (ready) {
              const display = (show) => {
                this.display(show);
              };
              display(false);
              const responses = (src) => {
                this.responses(src);
              };

              const data = new FormData();
              data.append(name, file);

              axios
                .post(url, data, {
                  headers: {
                    "Content-Type": "multipart/form-data",
                  },
                })
                .then(function (response) {
                  responses(response.request.responseText);
                })
                .catch(function (error) {
                  //
                })
                .finally(function () {
                  display(true);
                });
            }
          }}
        >
          <input
            id="url"
            type="text"
            style={{ width: 250 }}
            placeholder="URL Target"
            autoComplete={false}
            onChange={(e) => {
              this.setState({
                url: e.target.value,
              });
            }}
            onKeyUp={(e) => {
              this.submitOK();
            }}
          />
          <br />
          <input
            id="name"
            type="text"
            style={{ width: 250, display: "none" }}
            placeholder="elements -> input file -> name (default: file)"
            autoComplete={false}
            onChange={(e) => {
              this.setState({
                name: e.target.value,
              });
            }}
            onKeyUp={(e) => {
              this.submitOK();
            }}
          />
          <br />
          <input
            id="file"
            type="file"
            style={{ display: "none" }}
            name={name}
            onChange={(e) => {
              this.setState({
                file: e.target.files[0],
              });
              setTimeout(() => {
                this.submitOK();
              }, 100);
            }}
            onClick
          />
          <br />
          <br />
          <input
            id="submit"
            type="submit"
            value="upload!"
            style={{ backgroundColor: "transparent", display: "none" }}
          />
        </form>
        <div id="container_response" style={{ display: "none" }}>
          <h4>Response</h4>
          <div
            id="response"
            style={{
              textAlign: "left",
              border: "1px solid lime",
              padding: "0.01em 16px",
              borderRadius: "16px",
            }}
            dangerouslySetInnerHTML={{ __html: response_src }}
          ></div>
        </div>
        {alertModal}
      </div>
    );
  }
}

export default FormCSRF;
