import React, { Component } from "react";

const list = [
  {
    img: "https://cdn.aarp.net/content/dam/aarp/money/credit-and-debt/2018/03/1140-equifax-data-breach.imgcache.rev3dc7a123ca1d5e898a83d0f6e08945ac.jpg",
    kata: "passion",
  },
  {
    img: "https://sentekglobal.com/blog/wp-content/uploads/sites/2/2018/12/hackers-steal-millions-from-banks-via-malware-1140x641.jpg",
    kata: "lifestyle",
  },
  {
    img: "https://ak.picdn.net/shutterstock/videos/14019179/thumb/1.jpg",
    kata: "trick",
  },
  {
    img: "https://webdevolutions.blob.core.windows.net/blog/2019/05/Data-Breach-vs-Data-Hack-Devolutions.png",
    kata: "dangerous",
  },
  {
    img: "https://4.bp.blogspot.com/-c_pFuUKB4vM/WxJXS7zhSbI/AAAAAAAABYY/8Yo7BFJ6azkzq8x-5PCpUChvr5Vw9gTRACLcBGAs/s1600/ice_screenshot_20180602-153719.png",
    kata: "you",
  },
];

class HeroSlider extends Component {
  handleClick = (e) => {
    e.preventDefault();
  };
  render() {
    return (
      <>
        <div id="ei-slider" class="ei-slider">
          <ul class="ei-slider-large">
            {list.map((val) => {
              return (
                <>
                  <li>
                    <img src={val.img} alt={val.kata} class="responsiveslide" />
                    <div class="ei-title">
                      <h2>Hack is</h2>
                      <h3>{val.kata}</h3>
                    </div>
                  </li>
                </>
              );
            })}
          </ul>
          {/* <!-- slider-thumbs --> */}
          <ul class="ei-slider-thumbs">
            <li class="ei-slider-element">Current</li>
            {list.map((val) => {
              return (
                <>
                  <li>
                    <a href="!#" onClick={this.handleClick}>
                      {val.kata}
                    </a>
                    <img src={val.img} class="slideshowthumb" alt={val.kata} />
                  </li>
                </>
              );
            })}
          </ul>
        </div>
      </>
    );
  }
}

export default HeroSlider;
