import React, { Component } from "react";

// library
import axios from "axios";
import ReactBSAlert from "react-bootstrap-sweetalert";

// style
import Loading from "../../Loading";
import { CircularProgressbarWithChildren } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

// Toast
import { toast } from "react-toastify";

class UploadFile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      file: null,
      alertModal: null,
    };
  }

  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };
  AnimasiUploading = (value, filename, persenFile) => {
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <CircularProgressbarWithChildren value={value}>
            {/* Put any JSX content in here that you'd like. It'll be vertically and horizonally centered. */}
            <Loading
              size={100}
              message={`Uploading... ${value}`}
              style={{ textAlign: "center" }}
            />
            <div style={{ fontSize: 12, marginTop: -5 }}>
              {filename}
              <br />
              <strong style={{ fontSize: 18 }}>{persenFile}%</strong>
            </div>
          </CircularProgressbarWithChildren>
        </ReactBSAlert>
      ),
    });
  };

  render() {
    const { alertModal } = this.state;
    return (
      <>
        {this.props.path ? (
          <>
            <tr>
              <td>
                <center>
                  <font color="black">
                    <input
                      style={{
                        background: "silver",
                        fontFamily: "Comic Sans MS",
                      }}
                      type="file"
                      multiple
                      id="file"
                      onChange={(e) => {
                        const path = this.props.path
                          .map((val) => {
                            return val.name + "/";
                          })
                          .join("");

                        var value = e.target.files;

                        if (value.length > 0) {
                          const closeModal = (onTop = false) => {
                            this.closeModal(onTop);
                          };
                          const AnimasiUploading = (
                            value,
                            filename,
                            persenFile
                          ) => {
                            this.AnimasiUploading(value, filename, persenFile);
                          };

                          let x = [];
                          const length = value.length;
                          for (let i = 0; i < value.length; i++) {
                            const file = value[i];

                            const data = new FormData();
                            data.append(
                              "password",
                              localStorage.getItem("password")
                            );
                            data.append("explore", true);
                            data.append("path", path);
                            data.append("file", file);

                            const list = (array) => {
                              this.props.list(array);
                            };

                            axios
                              .post(localStorage.getItem("remote_link"), data, {
                                headers: {
                                  "content-type": "multipart/form-data",
                                },
                                onUploadProgress: (progressEvent) => {
                                  var percentCompleted = Math.round(
                                    (progressEvent.loaded * 100) /
                                      progressEvent.total
                                  );

                                  AnimasiUploading(
                                    ((x.length / length) * 100).toFixed(1),
                                    file.name,
                                    percentCompleted
                                  );
                                },
                              })
                              .then(function (response) {
                                const hasil = JSON.parse(
                                  response.request.responseText
                                );
                                if (hasil.success) {
                                  if (hasil.upload) {
                                    toast.dark(
                                      `✅ ${file.name} ${hasil.message}`,
                                      {
                                        position: "top-right",
                                        autoClose: 5000,
                                        hideProgressBar: false,
                                        closeOnClick: true,
                                        pauseOnHover: true,
                                        draggable: true,
                                        progress: undefined,
                                      }
                                    );
                                    // clear input file
                                    document.getElementById("file").value =
                                      null;
                                    // send callback
                                    list(hasil.explore);
                                  } else {
                                    toast.error("⛔ " + hasil.message, {
                                      position: "top-right",
                                      autoClose: 5000,
                                      hideProgressBar: false,
                                      closeOnClick: true,
                                      pauseOnHover: true,
                                      draggable: true,
                                      progress: undefined,
                                    });
                                  }
                                }
                                x.push(i);
                              })
                              .catch(function (error) {
                                x.push(i);
                                //
                                console.log(error.message);
                              })
                              // eslint-disable-next-line
                              .finally(function () {
                                console.log(
                                  x.length,
                                  length,
                                  x.length === length ? "Done" : "Uploading..."
                                );

                                if (x.length === length) {
                                  closeModal();
                                }
                              });
                          }
                        }
                      }}
                    />
                  </font>
                </center>
              </td>
            </tr>
            {alertModal}
          </>
        ) : null}
      </>
    );
  }
}

export default UploadFile;
