import React, { Component } from "react";

// Library
import ReactBSAlert from "react-bootstrap-sweetalert";

import {
  insertOptionPayload,
  nomer_urut,
} from "../../../components/anon_hackbar/Function";

import {
  bantuan_option_payload_union_based,
  bantuan_option_payload_string_based,
  bantuan_option_payload_waf_bypass,
} from "../../../pages/anon_hackbar/static_data";

export default class BantuanPayload extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //wajib
      alertModal: null,
    };
  }

  handleClick = (event) => {
    event.preventDefault();
  };

  ShowModal = (onShow, content, width = 500, onTop = false) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            color: "black",
            width: width,
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={true}
          closeOnClickOutside={false}
          customClass={{
            closeButton: "close-button-class",
          }}
          //
          onConfirm={() => this.closeModal(onTop)}
          onCancel={() => this.closeModal(onTop)}
          //
          afterMount={() => onShow()}
        >
          {content}
        </ReactBSAlert>
      ),
    });
  };
  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };

  render() {
    const { alertModal } = this.state;
    return (
      <>
        <span id="info-bantuan-payload" style={{ display: "none" }}>
          butuh bantuan? (Payload){" "}
          <button
            id="bantuan-payload"
            onClick={(e) => {
              this.ShowModal(
                function () {
                  document
                    .getElementById("sqli-select-payload-type")
                    .addEventListener("change", function () {
                      document.getElementById(
                        "sqli-select-payload"
                      ).style.display = "block";
                      if (this.value === "union") {
                        insertOptionPayload(
                          "sqli-select-payload",
                          bantuan_option_payload_union_based,
                          "Pilih order / group / union (union based) ..."
                        );
                      } else if (this.value === "string") {
                        insertOptionPayload(
                          "sqli-select-payload",
                          bantuan_option_payload_string_based,
                          "Pilih order / group / union (string based) ..."
                        );
                      } else if (this.value === "waf") {
                        insertOptionPayload(
                          "sqli-select-payload",
                          bantuan_option_payload_waf_bypass,
                          "Pilih order / group / union (WAF Bypass) ..."
                        );
                      }
                    });
                  document
                    .getElementById("sqli-select-payload")
                    .addEventListener("change", function () {
                      document.getElementById(
                        "div-sqli-input-kolom"
                      ).style.display = "block";
                      document.getElementById(
                        "span-sqli-select-payload"
                      ).style.display = "none";
                    });
                  document
                    .getElementById("sqli-input-kolom")
                    .addEventListener("keyup", function () {
                      document.getElementById(
                        "sqli-select-stabilizer"
                      ).style.display = "block";
                    });
                  document
                    .getElementById("sqli-select-stabilizer")
                    .addEventListener("change", function () {
                      document.getElementById(
                        "sqli-button-tempel"
                      ).style.display = "block";
                    });
                  document
                    .getElementById("sqli-input-kolom")
                    .addEventListener("keyup", function () {
                      if (this.value < 0 || this.value === "-") {
                        document.getElementById(
                          "span-sqli-input-kolom"
                        ).innerHTML =
                          '<font color="red">yang benar dong isi kolomnya bang jago...</font>';
                        document.getElementById(
                          "span-sqli-input-kolom"
                        ).style.display = "block";
                      } else {
                        document.getElementById(
                          "span-sqli-input-kolom"
                        ).style.display = "none";
                      }
                    });
                  document
                    .getElementById("sqli-button-tempel")
                    .addEventListener("click", function () {});
                },
                <>
                  <strong>
                    Bantuan <u>payload</u>
                  </strong>

                  <div>
                    <select id="sqli-select-payload-type" class="select-css">
                      <option disabled selected hidden>
                        Pilih payload type ...
                      </option>
                      <option value="union">Union Based</option>
                      <option value="string">String Based</option>
                      <option value="waf">WAF Bypass</option>
                    </select>

                    <select
                      id="sqli-select-payload"
                      class="select-css"
                      style={{ display: "none" }}
                    ></select>
                    <span
                      style={{ display: "none" }}
                      id="span-sqli-select-payload"
                    ></span>

                    <div
                      class="form__group field"
                      style={{ display: "none" }}
                      id="div-sqli-input-kolom"
                    >
                      <input
                        type="input"
                        style={{ color: "black" }}
                        class="form__field"
                        placeholder="Kolom..."
                        id="sqli-input-kolom"
                      />
                      <span
                        style={{ display: "none" }}
                        id="span-sqli-input-kolom"
                      ></span>
                    </div>

                    <select
                      id="sqli-select-stabilizer"
                      class="select-css"
                      style={{ display: "none" }}
                    >
                      <option disabled selected hidden>
                        Pilih stabilizer ...
                      </option>
                      <option value="--">-- (SQL Linux Style)</option>
                      <option value="--+">--+ (SQL Windows Styl)</option>
                      <option value="#"># (Hash "URL encode while use")</option>
                      <option value="--+-">--+- (SQL Comment)</option>
                      <option value=";%00">;%00 (Null Byte)</option>
                      <option value="'">' (Backtick)</option>
                    </select>
                    <br />
                    <a
                      class="button_bantuan"
                      id="sqli-button-tempel"
                      href="#tempel"
                      onClick={(e) => {
                        this.handleClick(e);

                        var error = 0;
                        var payload = document.getElementById(
                          "sqli-select-payload"
                        );
                        var payload_value = payload.value;
                        var payload_type =
                          payload.options[payload.selectedIndex].getAttribute(
                            "data-type"
                          );
                        var kolom =
                          document.getElementById("sqli-input-kolom").value;
                        var stabilizer = document.getElementById(
                          "sqli-select-stabilizer"
                        ).value;
                        console.log(payload_value, payload_type, kolom, stabilizer);

                        if (payload_value === "") {
                          document.getElementById(
                            "span-sqli-select-payload"
                          ).innerHTML =
                            '<font color="red">tolonglah pilih payload nya bang jago...</font>';
                          document.getElementById(
                            "span-sqli-select-payload"
                          ).style.display = "block";
                          error += 1;
                        }

                        if (kolom === "") {
                          document.getElementById(
                            "span-sqli-input-kolom"
                          ).innerHTML =
                            '<font color="red">tolonglah isi kolomnya bang jago...</font>';
                          document.getElementById(
                            "span-sqli-input-kolom"
                          ).style.display = "block";
                          error += 1;
                        } else if (kolom < 0) {
                          document.getElementById(
                            "span-sqli-input-kolom"
                          ).innerHTML =
                            '<font color="red">yang benar dong isi kolomnya bang jago...</font>';
                          document.getElementById(
                            "span-sqli-input-kolom"
                          ).style.display = "block";
                          error += 1;
                        }

                        if (error === 0) {
                          var tempel;
                          if (payload_type !== "union") {
                            tempel = kolom + stabilizer;
                            tempel = payload_value.replace("#kolom#", tempel);
                          } else {
                            //union select
                            tempel = nomer_urut(kolom);
                            tempel = tempel + stabilizer;
                            tempel = payload_value.replace(
                              "#urutan_kolom#",
                              tempel
                            );
                          }
                          document.getElementById("set-payload").value = tempel;
                          this.closeModal();
                        }
                      }}
                    >
                      TEMPEL!
                    </a>
                  </div>
                </>
              );
            }}
          >
            klik disini
          </button>
        </span>
        {alertModal}
      </>
    );
  }
}
