import React, { Component } from "react";
import { Link } from "react-router-dom";

// pages == menu
import pages from "../../../pages";

class Menu extends Component {
  render() {
    return (
      <>
        {pages
          .filter((e) => {
            return e.menu;
          })
          .map((val) => {
            return val.br ? (
              <>
                <Link
                  to={val.for + val.path}
                  style={{
                    color:
                      this.props.location.pathname === val.for + val.path
                        ? "rgb(0, 255, 0)"
                        : "#02BC8C",
                  }}
                >
                  {val.name}
                </Link>
                <br />
                <br />
              </>
            ) : (
              <>
                <Link
                  to={val.for + val.path}
                  style={{
                    color:
                      this.props.location.pathname === val.for + val.path
                        ? "rgb(0, 255, 0)"
                        : "#02BC8C",
                  }}
                >
                  {val.name}
                </Link>{" "}
              </>
            );
          })}
      </>
    );
  }
}

export default Menu;
