import React, { Component } from "react";

// Components
import InformationServer from "./InformationServer";
import Library from "./Library";
import Menu from "./Menu";
import UploadFile from "./UploadFile";

class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      file: null,
    };
  }
  home_path = () => {
    return JSON.parse(localStorage.getItem("home_path"))
      .map((val) => {
        return val.name + "/";
      })
      .join("");
  };
  stringPath = () => {
    return this.props.path
      .map((val) => {
        return val.name + "/";
      })
      .join("");
  };
  render() {
    return (
      <>
        <center>
          <b>
            <table
              width="700"
              border="0"
              cellpadding="3"
              cellspacing="1"
              align="center"
            >
              <tr>
                <td>
                  <font color="#02BC8C">
                    <center>
                      <p>
                        P34C3_<font color="white">KHYREIN</font>
                      </p>
                      <InformationServer info={this.props.info} />
                      <br />
                      <Library info={this.props.info} />
                      <br />
                      <Menu pages={this.props.pages} {...this.props} />
                      <a
                        style={{ color: "red" }}
                        href="#logout"
                        rel="noopener noreferrer"
                        onClick={(e) => {
                          e.preventDefault();
                          this.setState({
                            session: null,
                          });
                          localStorage.removeItem("session");
                          localStorage.removeItem("password");
                          localStorage.removeItem("remote_link");
                          localStorage.removeItem("last_path");

                          this.props.history.push("/remote_shell/");
                        }}
                      >
                        Logout
                      </a>
                      <br />
                      <br />
                    </center>
                  </font>
                </td>
              </tr>
              {this.props.isExplore ? (
                <font color="#02BC8C" style={{ fontSize: 15 }}>
                  <center>
                    {this.home_path() !== this.stringPath() ? (
                      <>
                        <font
                          style={{ color: "lime", cursor: "pointer" }}
                          onClick={(e) => {
                            e.preventDefault();
                            const path = this.home_path();
                            const path_now = this.stringPath();
                            if (path !== path_now) {
                              this.props.change_path(path);
                            }
                          }}
                        >
                          <u>Go Back to Home File</u>
                        </font>
                        <br />
                        <br />
                      </>
                    ) : null}
                    Current Path: <br />
                    {this.props.path.map((val, i) => {
                      return (
                        <>
                          <font
                            style={{ color: "lime", cursor: "pointer" }}
                            onClick={(e) => {
                              e.preventDefault();
                              if (i !== this.props.path.length - 1) {
                                this.props.change_path(val.url);
                              }
                            }}
                          >
                            <u>{val.name}</u>
                          </font>
                          /
                        </>
                      );
                    })}
                  </center>
                </font>
              ) : null}
              <br />
              <UploadFile
                {...this.props}
                list={(v) => {
                  this.props.list(v);
                }}
              />
              <br />
            </table>
          </b>
        </center>
      </>
    );
  }
}

export default index;
