import React, { Component } from "react";
import axios from "axios";

// Components
import Loading from "../../Loading";

class Quotes extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //wajib
      error: false,
      isLoaded: false,

      quote: {},
    };
  }

  componentDidMount() {
    const setState = (array) => {
      this.setState(array);
    };

    axios
      .get("http://api.quotable.io/random?minLength=100")
      .then(function (response) {
        setState({
          quote: JSON.parse(response.request.responseText),
        });
      })
      .catch(function (error) {
        console.log(error.message);
      })
      .finally(function () {
        setState({
          isLoaded: true,
        });
      });
  }

  render() {
    const { error, isLoaded, quote } = this.state;

    if (error) {
      return <>{error}</>;
    } else if (!isLoaded) {
      return (
        <div style={{ color: "white" }} className="center-page">
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </div>
      );
    } else {
      return (
        <>
          <div class="row">
            <div class="twelve columns">
              <div id="testimonials">
                <blockquote>
                  <p>
                    "{quote.content}"{" "}
                    <cite>
                      {quote.author} ({quote.tags[0]})
                    </cite>
                  </p>
                </blockquote>
                {/* <blockquote>
                  <p>
                    "Duis aute irure dolor in reprehenderit in voluptate velit
                    esse cillum dolore eu fugiat nulla pariatur. Excepteur sint
                    occaecat cupidatat non proident, sunt in culpa qui officia
                    deserunt mollit anim id est laborum. Lorem ipsum dolor sit
                    amet, consectetur adipisicing elit, sed do eiusmod tempor
                    incididunt ut labore et dolore magna aliqua. Ut enim ad
                    minim veniam, quis nostrud exercitation ullamco."{" "}
                    <cite>Sandra - LA</cite>
                  </p>
                </blockquote>
                <blockquote>
                  <p>
                    "Duis aute irure dolor in reprehenderit in voluptate velit
                    esse cillum dolore eu fugiat nulla pariatur. Excepteur sint
                    occaecat cupidatat non proident, sunt in culpa qui officia
                    deserunt mollit anim id est laborum. Lorem ipsum dolor sit
                    amet, consectetur adipisicing elit, sed do eiusmod tempor
                    incididunt ut labore et dolore magna aliqua. Ut enim ad
                    minim veniam, quis nostrud exercitation ullamco."{" "}
                    <cite>Jason - MA</cite>
                  </p>
                </blockquote> */}
              </div>
              {/* <!--end testimonials--> */}
            </div>
          </div>
        </>
      );
    }
  }
}

export default Quotes;
