var observe = null;
if (window.attachEvent) {
  observe = function (element, event, handler) {
    element.attachEvent("on" + event, handler);
  };
} else {
  observe = function (element, event, handler) {
    try {
      element.addEventListener(event, handler, false);
    } catch (err) {}
  };
}

function init_textarea(id) {
  var text = document.getElementById(id);

  function resize() {
    text.style.height = "auto";
    text.style.height = text.scrollHeight + "px";
  }
  /* 0-timeout to get the already changed text */
  function delayedResize() {
    window.setTimeout(resize, 0);
  }
  observe(text, "change", resize);
  observe(text, "cut", delayedResize);
  observe(text, "paste", delayedResize);
  observe(text, "drop", delayedResize);
  observe(text, "keydown", delayedResize);

  text.focus();
  text.select();
  resize();
}

function insertOptionQuery(id_select, option, type, placeholder) {
  var added = document.createElement("option");
  var select = document.getElementById(id_select);
  select.innerHTML = "";
  added.disabled = true;
  added.selected = true;
  added.value = "";
  added.hidden = true;
  added.innerHTML = placeholder;
  select.append(added);

  ////// isi semua option
  for (var i = 0; i < option.length; i++) {
    added = document.createElement("option");
    added.value = option[i].konten;
    added.dataset.type = type;
    added.innerHTML = option[i].info;
    select.append(added);
  }
}

function insertOptionPayload(id_select, option, placeholder) {
  var optgroup_option,
    add_optgroup,
    perihal_optgroup,
    optgroup_id,
    added = document.createElement("option"),
    i;
  var select = document.getElementById(id_select);
  select.innerHTML = "";
  added.disabled = true;
  added.selected = true;
  added.value = "";
  added.hidden = true;
  added.innerHTML = placeholder;
  select.append(added);

  ////// isi semua option
  //order
  add_optgroup = document.createElement("optgroup");
  perihal_optgroup = "ORDER";
  optgroup_id = `optgroup-${perihal_optgroup}-${id_select}`;
  add_optgroup.label = perihal_optgroup;
  add_optgroup.id = optgroup_id;
  select.append(add_optgroup);
  for (i = 0; i < option.order.length; i++) {
    optgroup_option = document.getElementById(optgroup_id);
    added = document.createElement("option");
    added.value = option.order[i];
    added.dataset.type = "order";
    added.innerHTML = option.order[i];
    optgroup_option.append(added);
  }

  //group
  add_optgroup = document.createElement("optgroup");
  perihal_optgroup = "GROUP";
  optgroup_id = `optgroup-${perihal_optgroup}-${id_select}`;
  add_optgroup.label = perihal_optgroup;
  add_optgroup.id = optgroup_id;
  select.append(add_optgroup);
  for (i = 0; i < option.group.length; i++) {
    optgroup_option = document.getElementById(optgroup_id);
    added = document.createElement("option");
    added.value = option.group[i];
    added.dataset.type = "group";
    added.innerHTML = option.group[i];
    optgroup_option.append(added);
  }

  //union
  add_optgroup = document.createElement("optgroup");
  perihal_optgroup = "UNION";
  optgroup_id = `optgroup-${perihal_optgroup}-${id_select}`;
  add_optgroup.label = perihal_optgroup;
  add_optgroup.id = optgroup_id;
  select.append(add_optgroup);
  for (i = 0; i < option.union.length; i++) {
    optgroup_option = document.getElementById(optgroup_id);
    added = document.createElement("option");
    added.value = option.union[i];
    added.dataset.type = "union";
    added.innerHTML = option.union[i];
    optgroup_option.append(added);
  }
}

function setClipboard(value) {
  var tempInput = document.createElement("input");
  tempInput.style = "position: absolute; left: -1000px; top: -1000px";
  tempInput.value = value;
  document.body.appendChild(tempInput);
  tempInput.select();
  document.execCommand("copy");
  document.body.removeChild(tempInput);
}

function nomer_urut(nomer) {
  var hasil = "";
  for (var i = 1; i <= nomer; i++) {
    if (i !== 1) {
      hasil += ",";
    }
    hasil += String(i);
  }
  return hasil;
}

export {
  init_textarea,
  insertOptionQuery,
  insertOptionPayload,
  setClipboard,
  nomer_urut,
};
