import React, { Component } from "react";

// Library
import Ping from "ping.js";

export default class PingGoogle extends Component {
  constructor(props) {
    super(props);

    this.p = new Ping();
  }
  ping = () => {
    var ping_info = document.getElementById("ping");
    this.p.ping("http://google.com", (err, data) => {
      if (err) {
        console.log("error loading resource", err);
      }

      if (data <= 1000) {
        data = `<font color="green">${data} ms</font>`;
      } else if (data <= 2000) {
        data = `<font color="yellow">${data} ms</font>`;
      } else {
        data = `<font color="red">${data} ms</font>`;
      }
      ping_info.innerHTML = data;
    });
  };

  componentDidMount() {
    //ping test
    const ping = () => {
      this.ping();
    };
    this.setPing = setInterval(ping, 1000);
  }
  componentWillUnmount() {
    clearInterval(this.setPing);
  }

  render() {
    return (
      <>
        <div
          class="header"
          id="myHeader"
          style={{
            height: "60px",
            // paddingTop: "1px",
            // paddingBottom: "1px",
            backgroundColor: "#555",
            color: "#f1f1f1",
            zIndex: 9999999,
          }}
        >
          <h2>
            ping: <span id="ping"></span>
          </h2>
        </div>
      </>
    );
  }
}
