import React, { Component } from "react";

// Library
import ReactBSAlert from "react-bootstrap-sweetalert";

// Components
import Header from "../../components/Header";
import PingGoogle from "../../components/anon_hackbar/PingGoogle";
import Tutorial from "../../components/anon_hackbar/Tutorial";
import BantuanQuery from "../../components/anon_hackbar/bantuan/BantuanQuery";
import BantuanPayload from "../../components/anon_hackbar/bantuan/BantuanPayload";

// style
import Loading from "../../components/Loading";

import {
  init_textarea,
  setClipboard,
} from "../../components/anon_hackbar/Function";

import { dork_sql } from "./static_data";

import "../../assets/css/anon_hackbar/index.css";

class AnonHackBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //wajib
      error: false,
      isLoaded: false,
      alertModal: null,

      mode: 0,
      result: null,
    };
  }

  handleClick = (event) => {
    event.preventDefault();
  };

  AnimasiLoading = () => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </ReactBSAlert>
      ),
    });
  };

  ShowModal = (onShow, content, width = 500, onTop = false) => {
    this.closeModal();
    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            color: "black",
            width: width,
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={true}
          closeOnClickOutside={false}
          customClass={{
            closeButton: "close-button-class",
          }}
          //
          onConfirm={() => this.closeModal(onTop)}
          onCancel={() => this.closeModal(onTop)}
          //
          afterMount={() => onShow()}
        >
          {content}
        </ReactBSAlert>
      ),
    });
  };
  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };

  componentDidMount() {
    window.onbeforeunload = function (e) {
      e = e || window.event;
      // For IE and Firefox prior to version 4
      if (e) {
        e.returnValue = "Sure?";
      }
      // For Safari
      return "Sure?";
    };

    // header fixed sticky
    window.onscroll = function () {
      myFunction();
    };

    var header = document.getElementById("myHeader");
    var sticky = header.offsetTop;

    function myFunction() {
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
      } else {
        header.classList.remove("sticky");
      }
    }

    // kembali lagi ke app
    document.addEventListener(
      "visibilitychange",
      function () {
        if (document.visibilityState !== "hidden") {
          // Swal.fire({
          //   title: "Welcome Back!",
          //   allowOutsideClick: false,
          //   showConfirmButton: false,
          //   timer: 1500,
          // });
          console.log("welcome back!");
        }
      },
      false
    );
  }

  execute = () => {
    //hide all bantuan
    document.getElementById("info-bantuan-payload").style.display = "none";

    var result = "";
    var set_url = document.getElementById("set-url").value;

    if (set_url !== "" && set_url !== "'" && set_url !== "%27") {
      if (!set_url.includes("http://")) {
        if (!set_url.includes("https://")) {
          set_url = "http://" + set_url;
        }
      }

      console.log(this.state.mode);

      if (this.state.mode === 1) {
        //url+payload
        result = set_url.replace(
          "#payload#",
          document.getElementById("set-payload").value
        );
      } else if (this.state.mode === 2) {
        //url+payload+query
        var res_payload = set_url.replace(
          "#payload#",
          document.getElementById("set-payload").value
        );

        var set_query = document.getElementById("set-query").value;
        result = res_payload.replace("#query#", set_query);
      } else {
        //url
        result = set_url;
      }

      this.setState({
        result,
      });

      document.getElementById("preview").src = result;
      document.getElementById("preview").style.display = "block";
      document.getElementById("copy").style.display = "block";
    }

    setTimeout(() => {
      window.scrollTo(0, document.body.scrollHeight);
    }, 1000);
  };

  render() {
    document.title = this.props.title;
    const {
      // wajib
      alertModal,
    } = this.state;

    return (
      <div style={{ color: "white" }}>
        <div align="center">
          <Header app_name={this.props.title.split(" | ")[0]} />

          <PingGoogle />

          <br />
          <br />

          <Tutorial dork_sql={dork_sql} />

          <br />
          <br />

          <font color="cyan"> URL Site :</font>
          <br />
          <input
            id="set-url"
            style={{ color: "black", width: "100%" }}
            placeholder="Masukan URL..."
            onKeyUp={(e) => {
              if (e.target.value.length > 0) {
                //show execute
                document.getElementById("execute").style.display = "";
              } else {
                //
                document.getElementById("execute").style.display = "none";
              }

              if (e.target.value.includes("#payload#")) {
                document.getElementById("wrap-payload").style.display = "block";
                init_textarea("set-payload");
                this.setState({
                  mode: 1,
                });
              } else {
                document.getElementById("wrap-payload").style.display = "none";
                this.setState({
                  mode: 0,
                });
              }
            }}
            onBlur={(e) => {
              if (e.target.value.includes("#payload#")) {
                document.getElementById("wrap-payload").style.display = "block";
                init_textarea("set-payload");
                this.setState({
                  mode: 1,
                });
              } else {
                document.getElementById("wrap-payload").style.display = "none";
                this.setState({
                  mode: 0,
                });
              }
            }}
            onKeyPress={(event) => {
              if (event.which === 13) {
                // enter
                event.preventDefault();
                this.execute();
              }
            }}
          />
          <br />
          <div id="wrap-payload" style={{ display: "none" }}>
            <font color="cyan"> Payload :</font>
            <br />
            <textarea
              id="set-payload"
              placeholder="order by / union select..."
              cols="40"
              rows="2"
              onKeyUp={(e) => {
                if (e.target.value.includes("#query#")) {
                  document.getElementById("wrap-query").style.display = "block";
                  init_textarea("set-query");
                  this.setState({
                    mode: 2,
                  });
                } else {
                  document.getElementById("wrap-query").style.display = "none";
                  this.setState({
                    mode: 1,
                  });
                }
              }}
              onBlur={(e) => {
                if (e.target.value.includes("#query#")) {
                  document.getElementById("wrap-query").style.display = "block";
                  init_textarea("set-query");
                  this.setState({
                    mode: 2,
                  });
                } else {
                  document.getElementById("wrap-query").style.display = "none";
                  this.setState({
                    mode: 1,
                  });
                }
                setTimeout(() => {
                  document.getElementById(
                    "info-bantuan-payload"
                  ).style.display = "none";
                }, 30000);
              }}
              onFocus={(e) => {
                document.getElementById("info-bantuan-payload").style.display =
                  "block";
              }}
            ></textarea>
            <br />
            <BantuanPayload />
          </div>
          <div id="wrap-query" style={{ display: "none" }}>
            <font color="cyan"> Query :</font>
            <br />
            <textarea
              id="set-query"
              placeholder="Tulis Query..."
              cols="40"
              rows="2"
              onFocus={(e) => {
                document.getElementById("info-bantuan-query").style.display =
                  "block";
              }}
              onBlur={(e) => {
                setTimeout(() => {
                  document.getElementById("info-bantuan-query").style.display =
                    "none";
                }, 30000);
              }}
            ></textarea>
            <br />

            <BantuanQuery />
          </div>
          <br />
          <a
            id="execute"
            style={{ display: "none" }}
            class="button"
            href="#submit"
            onClick={(e) => {
              e.preventDefault();
              this.execute();
            }}
          >
            EXECUTE!
          </a>
          <br />
        </div>
        <br />
        <br />
        <div align="center">
          <a
            id="copy"
            class="button"
            href="#copy"
            onClick={(e) => {
              e.preventDefault();

              var copyText = this.state.result;
              setClipboard(copyText);
              alert("Copied: " + copyText);
            }}
            style={{ display: "none" }}
          >
            COPY LINK HASIL!
          </a>
          <input type="hidden" id="hasil" />
          <iframe
            title="view"
            is="x-frame-bypass"
            id="preview"
            frameborder="0"
            width="100%"
            height={window.innerHeight - 60}
            style={{
              display: "none",
              backgroundColor: "white",
            }}
          ></iframe>
        </div>
        {alertModal}
      </div>
    );
  }
}

export default AnonHackBar;
