import React, { Component } from "react";

// library
import axios from "axios";
import $ from "jquery";
import ReactBSAlert from "react-bootstrap-sweetalert";

// style
import Loading from "../../components/Loading";
import { CircularProgressbarWithChildren } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

// Components
import Header from "../../components/Header";
import Table from "../../components/Table";

const url = "https://p34c3-khyrein.linuxploit.com";

export default class PanelDomainLookUP extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wajib
      alertModal: null,

      multisite: false,
      // single
      url: null,

      // multi
      list: [],
      count: 0,
      result: [],
      ip_not_twin: [],
      tombol_close: false,
    };
  }

  closeModal = (scrollToTop = false) => {
    this.setState({ alertModal: null });
    if (scrollToTop) {
      this.scrollOnTop();
    }
  };
  AnimasiProcessing = (length, domain) => {
    const { multisite, result, tombol_close } = this.state;
    var persen = 100;

    if (multisite) {
      persen = ((result.length / length) * 100).toFixed(1);

      console.log(result.length, length);
      if (result.length >= length) {
        setTimeout(() => {
          this.closeModal();
        }, 3000);
      }
    }

    this.setState({
      alertModal: (
        <ReactBSAlert
          style={{
            display: "block",
            background: "",
            color: "white",
          }}
          //
          showConfirm={false}
          showCancel={false}
          showCloseButton={false}
          closeOnClickOutside={false}
          //
          onConfirm={() => this.closeModal()}
          onCancel={() => this.closeModal()}
        >
          <CircularProgressbarWithChildren value={persen}>
            {/* Put any JSX content in here that you'd like. It'll be vertically and horizonally centered. */}
            <Loading size={80} message={``} style={{ textAlign: "center" }} />
            <h4>Processing... {parseInt(persen)}%</h4>
            <div style={{ fontSize: 18 }}>{domain}</div>
            {tombol_close ? (
              <>
                <br />
                <br />
                Press <b>ESC</b> to close loading
              </>
            ) : (
              <>
                {multisite ? (
                  <>
                    {result.length} of {length}
                  </>
                ) : (
                  <>Please wait...</>
                )}
              </>
            )}
          </CircularProgressbarWithChildren>
        </ReactBSAlert>
      ),
    });
  };

  sender = (domain, IP, Error, Finish, Progress) => {
    // make form
    const data = new FormData();
    data.append("domain", domain);

    // sending...
    axios
      .post(url + "/get_ip.php", data, {
        onUploadProgress: (progressEvent) => {
          Progress(progressEvent);
        },
      })
      .then(function (response) {
        const ip = response.request.responseText;
        IP(ip);
      })
      .catch(function (error) {
        Error(error.message);
      })
      .finally(function () {
        Finish();
      });
  };

  StartInterval = () => {
    // di mulai dari nol ya :V
    this.setState({
      count: 0,
      result: [],
      tombol_close: false,
    });
    var ip_list = [],
      ip_not_twin = [],
      regExp = /[a-zA-Z]/g;

    // start loop
    this.interval = setInterval(() => {
      // define
      var count = this.state.count;
      const length = this.state.list.length - 1;

      // select list domain
      const domain = this.state.list[count];

      // make form
      const data = new FormData();
      data.append("domain", domain);

      // pancingan
      const AnimasiProcessing = (length, domain) => {
        this.AnimasiProcessing(length, domain);
      };
      const setState = (array) => {
        this.setState(array);
      };

      // sending...
      this.sender(
        domain,
        function (ip) {
          var ip_ok = null;
          if (!regExp.test(ip)) {
            ip_ok = ip;
            if (!ip_not_twin.some((v) => ip.includes(v))) {
              ip_not_twin.push(ip);
            }
          } else {
            // not found
            ip_ok = null;
          }
          ip_list.push({ domain, ip: ip_ok });
        },
        function (error) {
          console.log(error);
        },
        function () {
          // finish sender
        },
        function (progress) {
          AnimasiProcessing(length, domain);
        }
      );

      // handle ending
      if (length === count) {
        // finishing
        this.setState({
          result: ip_list,
          ip_not_twin,
          tombol_close: true,
        });
        clearInterval(this.interval);
      } else {
        // counter
        count++;
        setState({
          count,
        });
      }
    }, 300);
  };

  componentDidMount() {
    const closeModal = () => {
      this.closeModal();
    };
    $(document).keyup(function (e) {
      if (e.key === "Escape") {
        // escape key maps to keycode `27`
        closeModal();
      }
    });
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }

  getDate() {
    var today = new Date();
    return (
      today.getFullYear() +
      "_" +
      (today.getMonth() + 1) +
      "_" +
      today.getDate() +
      "-" +
      today.getHours() +
      "_" +
      today.getMinutes() +
      "_" +
      today.getSeconds()
    );
  }

  downloadList = (insert_domain) => {
    const { result, ip_not_twin } = this.state;
    const element = document.createElement("a");

    var src;
    if (insert_domain) {
      src = result
        .map((val) => {
          return val.domain + "|" + val.ip;
        })
        .join("\n");
    } else {
      src = ip_not_twin
        .map((val) => {
          return val;
        })
        .join("\n");
    }
    const file = new Blob([src], {
      type: "text/plain;charset=utf-8",
    });

    element.href = URL.createObjectURL(file);
    element.download = "lookup-" + this.getDate() + ".txt";
    document.body.appendChild(element);

    element.click();

    element.remove();
  };

  render() {
    document.title = this.props.title;
    const { alertModal, result, ip_not_twin, multisite, url } = this.state;
    return (
      <>
        <Header app_name={this.props.title.split(" | ")[0]} />
        <div style={{ textAlign: "center" }} id="container">
          <select
            style={{ width: 100 }}
            onChange={(e) => {
              const multisite = e.target.value === "multi" ? true : false;
              this.setState({
                multisite,
              });
            }}
          >
            <option value="single">single site</option>
            <option value="multi">multi site</option>
          </select>
          <br />
          <br />
          {multisite ? (
            <div style={{ textAlign: "center" }}>
              <h5>input list domain file (.txt) (rekomendasi max 200 list)</h5>
              <input
                id="file"
                type="file"
                onChange={(e) => {
                  const setState = (array) => {
                    this.setState(array);
                  };

                  var fr = new FileReader();
                  fr.onload = function () {
                    const filename = e.target.files[0].name;
                    const ext = filename.substr(filename.lastIndexOf(".") + 1);
                    if (ext === "txt") {
                      setState({
                        list: fr.result.split("\n"),
                      });
                    } else {
                      console.log("please select .txt file !!");

                      document.getElementById("file").value = "";
                    }
                  };

                  fr.readAsText(e.target.files[0]);

                  document.getElementById("process").style.display = "";
                }}
              />
            </div>
          ) : (
            <div style={{ textAlign: "center" }}>
              <h5>input URL site</h5>
              <input
                type="text"
                style={{ width: 666 }}
                placeholder="URL Site"
                value={url}
                onChange={(e) => {
                  this.setState({
                    url: e.target.value,
                  });
                }}
                onKeyUp={(e) => {
                  var value = e.target.value;
                  if (value.includes("https://")) {
                    value = value.replaceAll("https://", "");
                  } else {
                    value = value.replaceAll("http://", "");
                  }
                  value = value.replaceAll("/", "");
                  if (value.length > 0) {
                    this.setState({
                      url: value, // tanam balik
                    });
                    document.getElementById("process").style.display = "";
                  } else {
                    document.getElementById("process").style.display = "none";
                  }
                }}
              />
            </div>
          )}

          <br />
          <br />
          <button
            id="process"
            style={{ display: "none" }}
            onClick={(e) => {
              const { multisite } = this.state;
              // all hidden
              document.getElementById("container").style.display = "none";

              if (multisite) {
                this.StartInterval();
              } else {
                // single site
                console.log(url);

                const setState = (array) => {
                  this.setState(array);
                };
                const closeModal = () => {
                  this.closeModal();
                };

                this.AnimasiProcessing(1, url);
                this.sender(
                  url,
                  function (ip) {
                    setState({
                      result: [
                        {
                          domain: url,
                          ip,
                        },
                      ],
                      ip_not_twin: [ip],
                      tombol_close: true,
                    });
                  },
                  function (error) {
                    //
                  },
                  function () {
                    //
                    closeModal();
                  },
                  function (progress) {
                    //
                  }
                );
              }
            }}
          >
            process!
          </button>
        </div>

        {result.length > 0 ? (
          <>
            <div style={{ textAlign: "center" }}>
              <input
                type="button"
                style={{
                  background: "transparent",
                  color: "lime",
                  width: 350,
                }}
                value={`(${result.length}) Download, (format: domain | ip) (txt)`}
                onClick={(e) => {
                  e.preventDefault();
                  this.downloadList(true);
                }}
              />
              &nbsp;&nbsp;&nbsp;
              <input
                type="button"
                style={{
                  background: "transparent",
                  color: "lime",
                  width: 300,
                }}
                value={`(${ip_not_twin.length}) Download, (format: ip) (ip tidak sama) (txt)`}
                onClick={(e) => {
                  e.preventDefault();
                  this.downloadList(false);
                }}
              />
            </div>
            <br />
            <br />
            <Table
              column={["Domain", "IP"]}
              row={
                <>
                  {result.map((val) => {
                    return (
                      <tr>
                        <td class="td_home">
                          <center>{val.domain}</center>
                        </td>
                        <td class="td_home">
                          <center>{val.ip}</center>
                        </td>
                      </tr>
                    );
                  })}
                </>
              }
            />
          </>
        ) : null}
        <br />
        <br />
        {alertModal}
      </>
    );
  }
}
