import React, { Component } from "react";
import axios from "axios";

// Components
import Loading from "../../Loading";

class GithubList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wajib
      error: false,
      isLoaded: false,
      alertModal: null,

      // list repos
      repos: [],
    };
  }

  componentDidMount() {
    const setState = (array) => {
      this.setState(array);
    };
    const callback = (src) => {
      this.props.callback(src);
    };

    axios
      .get("https://api.github.com/users/p34c3-khyrein/repos")
      .then(function (response) {
        const repos = JSON.parse(response.request.responseText);
        setState({
          repos,
        });
        callback(repos);
      })
      .catch(function (error) {
        console.log(error.message);
      })
      .finally(function () {
        setState({
          isLoaded: true,
        });
      });
  }

  render() {
    // eslint-disable-next-line
    const { error, isLoaded, alertModal, repos } = this.state;

    if (error) {
      return <>{error}</>;
    } else if (!isLoaded) {
      return (
        <div style={{ color: "white" }} className="center-page">
          <Loading
            size={100}
            message={"Loading..."}
            style={{ textAlign: "center" }}
          />
        </div>
      );
    } else {
      return (
        <>
          <div class="row">
            <div class="twelve columns">
              <div class="centersectiontitle">
                <h4>Github Repository</h4>
              </div>
            </div>
            {repos
              .filter((val) => {
                return !this.props.except.some((v) => val.name.includes(v));
              })
              .map((repo) => {
                return (
                  <>
                    <div class="four columns">
                      <h5 style={{ textAlign: "center" }}>
                        {repo.name.replaceAll("-", " ")}
                      </h5>
                      <p>{repo.description}</p>
                      <br />
                      <p>
                        <a
                          href={repo.html_url}
                          target="_blank"
                          rel="noreferrer"
                          class="readmore"
                        >
                          Go To Repo !!
                        </a>
                      </p>
                    </div>
                  </>
                );
              })}
          </div>
        </>
      );
    }
  }
}

export default GithubList;
