import React, { Component } from "react";

class Library extends Component {
  render() {
    return (
      <>
        |{" "}{this.props.info.library.map((val) => {
          if (val === "<br />") {
            return (
              <>
                <br /> |{" "}
              </>
            );
          }
          return (
            <>
              {Object.keys(val)}
              {": "}
              {Object.values(val)[0] ? (
                <>
                  <font color="lime">ON</font> |{" "}
                </>
              ) : (
                <>
                  <font color="red">OFF</font> |{" "}
                </>
              )}
            </>
          );
        })}
        <br />
      </>
    );
  }
}

export default Library;
